import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import { withRouter } from 'react-router-dom';
import Navbar from "../../components/Navbar/Navbar";
import Select from 'react-select';
import { getOrderDelay, sendDelayEmail } from '../../actions/order-delay-actions'
import { Input } from 'semantic-ui-react'
import CustomLoader from "../App/CustomLoader";
import PropTypes from 'prop-types';
import { OD_ORDER_STATUS, OD_ORDER_TYPE, OD_TIMEZONES } from '../../actions/constants'
import moment from 'moment';
import './OrderDelayMonitor.css'
import {DatetimePickerTrigger} from 'rc-datetime-picker';
import ReactExport from "react-data-export";
import { getUserInfo } from '../../actions/auth-actions'

const ExcelFile = ReactExport.ExcelFile;
const ExcelSheet = ReactExport.ExcelFile.ExcelSheet;
const ExcelColumn = ReactExport.ExcelFile.ExcelColumn

class OrderDelayMonitor extends Component {

    constructor(props){
        super(props);
        let availableModules = [];
        this.state={
            availableModules:availableModules,
            availableOrderStatuses:OD_ORDER_STATUS,
            availableOrderType:OD_ORDER_TYPE,
            availableTimezones: OD_TIMEZONES,
            startTime: moment().set({hour:0,minute:0,second:0}),
            endTime:moment().set({hour:23,minute:59,second:59}),
            threshold:180,
            stores:""
        }
    }

    componentDidMount() {
        this.props.getUserInfo()
    }

    updateModules = selected => {
        this.setState({selectedModules: selected})
    };

    handleChange = (moment) => {
        this.setState({
            startTime:moment
        });
    }

    getAsList(config){
        let result = [];
        for(let i=0; i<config.length; i++){
            if(config[i].checked)
                result.push(config[i].value)
        }
         return result;
    }
    getAsListTimezones(config){
        let result = [];
        for(let i=0; i<config.length; i++){
            if(config[i].checked)
                result.push(config[i].value)
        }
        if(result.length==config.length) return []
        return result;
    }

    getStoresFromString(storeString){
        if(storeString.trim().length<1){
            return [];
        }
        else{
            let stores = storeString.trim().split(',')
            return stores;
        }
    }

    submit = () => {
        console.log(this.state.stores)
        if(new moment(this.state.startTime)>new moment(this.state.endTime)){
            alert("Start time cannot be more than end time.")
        } else if(this.getStoresFromString(this.state.stores).length>50){
            alert("Maximum 50 stores are allowed in input.")
        }
        else{
            let body = {
                "startTime": new moment(this.state.startTime).format('YYYY-MM-DD HH:mm:ss'),
                "endTime": new moment(this.state.endTime).format('YYYY-MM-DD HH:mm:ss'),
                "timeZone": this.getAsListTimezones(this.state.availableTimezones),
                "orderType": this.getAsList(this.state.availableOrderType),
                "storeId": this.getStoresFromString(this.state.stores),
                "orderStatus": this.getAsList(this.state.availableOrderStatuses),
                "threshold": parseInt(this.state.threshold)
            }
            this.props.getOrderDelay(body)
        }

    }

    sendDelayMail(orderDelay, auth){
        let body = {
            "startTime": new moment(this.state.startTime).format('YYYY-MM-DD HH:mm:ss'),
            "endTime": new moment(this.state.endTime).format('YYYY-MM-DD HH:mm:ss'),
            "userEmail": auth.user.email,
            "timezoneSplit": this.getTimezoneSplit(orderDelay.orderDelayData.delayedOrders),
            "impactedStores":this.getImpactedStores(orderDelay.orderDelayData.delayedOrders).toString(),
            "averageDelay":this.getAverageDelay(orderDelay.orderDelayData.delayedOrders),
            "orderTypeSplit":this.getOrderTypeSplit(orderDelay.orderDelayData.delayedOrders),
            "totalOrderCount": orderDelay.orderDelayData.totalOrderCount,
            "delayedOrderCount": orderDelay.orderDelayData.delayedOrderCount,
            "unackOrderCount": orderDelay.orderDelayData.unackOrderCount,
            "error": null
        }
        this.props.sendDelayEmail(body)
        console.log(body)
    }


    getTimezoneSplit(delayedOrders){
        let timezones = new Object();
        for(let i=0; i<delayedOrders.length; i++){
            let tz = delayedOrders[i].TIMEZONE;
            if(timezones.hasOwnProperty(tz)){
                timezones[tz] += + 1;
            } else{
                timezones[tz] = 1
            }
        }
        let result = []
        for(let i=0; i<Object.keys(timezones).length; i++){
            let temp = {
                name:Object.keys(timezones)[i],
                value:timezones[Object.keys(timezones)[i]]
            }
            result.push(temp)
        }
        return result;
    }
    getOrderTypeSplit(delayedOrders){
        let orderTypes = new Object();
        for(let i=0; i<delayedOrders.length; i++){
            let tz = delayedOrders[i].EXTERNAL_ORDER_TYPE;
            if(orderTypes.hasOwnProperty(tz)){
                orderTypes[tz] += + 1;
            } else{
                orderTypes[tz] = 1
            }
        }
        let result = []
        for(let i=0; i<Object.keys(orderTypes).length; i++){
            let temp = {
                name:Object.keys(orderTypes)[i],
                value:orderTypes[Object.keys(orderTypes)[i]]
            }
            result.push(temp)
        }
        return result;
    }
    updateTimezones(index){
        let timezones = this.state.availableTimezones;
        timezones[index].checked = !timezones[index].checked;
        this.setState({availableTimezones:timezones})
    }
    updateOrderStatuses(index){
        let orderStatuses = this.state.availableOrderStatuses;
        orderStatuses[index].checked = !orderStatuses[index].checked;
        this.setState({availableOrderStatuses:orderStatuses})
    }
    updateOrderTypes(index){
        let orderTypes = this.state.availableOrderType;
        orderTypes[index].checked = !orderTypes[index].checked;
        this.setState({availableOrderType:orderTypes})
    }
    getImpactedStores(orders){
        let stores = {};
        for(let i=0; i<orders.length; i++){
            stores[orders[i].GLOBAL_STORE_ID] = orders[i].GLOBAL_STORE_ID;
        }
        console.log(Object.keys(stores))
        return  Object.keys(stores).length;
    }

    getDelayPrediction(orders, minutes){
        let count = 0;
        for(let i=0; i<orders.length; i++){
            if(parseInt(orders[i].TIME_REMAINING_FOR_SLA_BREACH)!=0 && parseInt(orders[i].TIME_REMAINING_FOR_SLA_BREACH)<minutes){
                count++;
            }
        }
        return count;
    }
    getAverageDelay(orders){
        let totalOrders = orders.length;
        let totalDetay = 0;
        for(let i=0; i<orders.length; i++){
              totalDetay+=parseInt(orders[i].DELAY)
        }
        if(totalOrders<1) return 0;
        return (totalDetay/totalOrders).toFixed(1)
    }
    render() {
        let { orderDelay, auth } = this.props
        return (
            <div>
                {orderDelay.loading && <CustomLoader/>}
                <Navbar/>
                <div className="jumbotron" style={{minHeight:'100vh'}}>
                    <h1>Order Delay Analysis</h1>
                    <ul className="breadcrumb">
                        <li><Link to="home">Home</Link></li>
                        <li><Link to="cutoffMonitor">Cutoff Manager</Link></li>
                        <li>Order Delay Analyser</li>
                    </ul>
                    <hr size="30"/>
                    <div className="row">
                        <div className={"col-md-5"}>
                            <div className={"currentDateNote"}>* all outputs are in UTC format. Current UTC time is {new moment().utc().format('YYYY-MM-DD HH:mm:ss')}</div>
                            <div className={"orderDelayForm"}>
                                <div className={"row"}>
                                    <div className="col-md-5">
                                        <label>Select Cutoff Start Time</label>
                                        <DatetimePickerTrigger
                                            moment={this.state.startTime}
                                            onChange={(moment) => this.setState({startTime:moment})}>
                                            <input type="text" value={this.state.startTime.format('YYYY-MM-DD HH:mm:ss')} className={"dateInput"} />
                                        </DatetimePickerTrigger>
                                    </div>
                                    <div className="col-md-5">
                                        <label>Select Cutoff End Time</label>
                                        <DatetimePickerTrigger
                                            moment={this.state.endTime}
                                            onChange={(moment) => this.setState({endTime:moment})}>
                                            <input type="text" value={this.state.endTime.format('YYYY-MM-DD HH:mm:ss')} className={"dateInput"} />
                                        </DatetimePickerTrigger>
                                    </div>
                                    <div className="col-md-2">
                                        <label>Threshold</label>
                                        <input type="text" value={this.state.threshold} className={"dateInput"} onChange={((event)=>this.setState({threshold:event.target.value}))}/>
                                    </div>
                                    <div className="col-md-12">
                                        <br/>
                                        <label>Stores (optional)</label>
                                        <textarea type="text" value={this.state.stores} className={"dateInput"} onChange={((event)=>this.setState({stores:event.target.value}))}/>
                                        <div className={"storeInputMessage"}>*Maximum 50 stores allowed per iteration</div>
                                    </div>
                                    <div className="col-md-12">
                                        <br/><br/>
                                        <label>Select Order Type</label><br/>
                                        <div className={"row"} style={{paddingLeft:'1.3rem'}}>
                                            {
                                                this.state.availableOrderType.map((item, index) => (
                                                    <div className={"col-md-3"}>
                                                        <input type="checkbox" className="form-check-input" checked={item.checked} onClick={()=>this.updateOrderTypes(index)}/> {item.label}
                                                    </div>
                                                ))
                                            }
                                        </div>
                                    </div>
                                    <div className="col-md-12">
                                        <br/><br/>
                                        <label>Select Order Status</label><br/>
                                        <div className={"row"} style={{paddingLeft:'1.3rem'}}>
                                            {
                                                this.state.availableOrderStatuses.map((item, index) => (
                                                    <div className={"col-md-3"}>
                                                        <input type="checkbox" className="form-check-input" checked={item.checked}  onClick={() => this.updateOrderStatuses(index)}/> {item.label}
                                                    </div>
                                                ))
                                            }
                                        </div>
                                    </div>
                                    <div className="col-md-12">
                                        <br/><br/>
                                        <label>Select Timezone</label><br/>
                                        <div className={"row"} style={{paddingLeft:'1.3rem'}}>
                                            {
                                                this.state.availableTimezones.map((item, index) => (
                                                    <div className={"col-md-3"}>
                                                        <input type="checkbox" className="form-check-input" checked={item.checked} onClick={() => this.updateTimezones(index)}/> {item.label}
                                                    </div>
                                                ))
                                            }
                                        </div>
                                    </div>
                                    <div className="col-md-3" style={{marginTop:"2rem"}}>
                                        <button type="button" className="btn btn-default" style={{"backgroundColor":"#35b96f", "color":"white", "width":"100%", outline:'none'}} onClick={() => this.submit()}>Submit</button>
                                    </div>
                                </div>
                            </div>
                        </div>
                        {orderDelay.orderDelayData &&
                        <div className={"col-md-7"}>
                            <div className={"row"}>

                                <div className={"col-md-12"}>
                                    <h3>Order Drop Summary</h3>
                                    <hr size="30"/>
                                    <div className={"orderStats"}>
                                        <div className={"orderStatName"}>Total Orders</div>
                                        <div className={"orderStatNumber"}>{orderDelay.orderDelayData.totalOrderCount}</div>
                                    </div>
                                    <div className={"orderStats"}>
                                        <div className={"orderStatName"}>Delayed Orders</div>
                                        <div className={"orderStatNumber"}>{orderDelay.orderDelayData.delayedOrderCount}</div>
                                    </div>
                                    <div className={"orderStats"}>
                                        <div className={"orderStatName"}>Unacknowledged Orders</div>
                                        <div className={"orderStatNumber"}>{orderDelay.orderDelayData.unackOrderCount}</div>
                                    </div>
                                    <div className={"orderStats"}>
                                        <div className={"orderStatName"}>Impacted Stores</div>
                                        <div className={"orderStatNumber"}>{this.getImpactedStores(orderDelay.orderDelayData.delayedOrders)}</div>
                                    </div>
                                    <div className={"orderStats"}>
                                        <div className={"orderStatName"}>Average Delay (minutes)</div>
                                        <div className={"orderStatNumber"}>{this.getAverageDelay(orderDelay.orderDelayData.delayedOrders)}</div>
                                    </div>
                                </div>
                                {orderDelay.orderDelayData.delayedOrders.length>0 && <div className={"col-md-4"}>
                                    <div className={"delayDetails"}>
                                        <div className={"delayDetailsHead"}>Order delay by timezone</div>
                                        {
                                            this.getTimezoneSplit(orderDelay.orderDelayData.delayedOrders).map((item, index) => (
                                                <div className={"orderStats"}>
                                                    <div className={"orderStatName"}>{item.name}</div>
                                                    <div className={"orderStatNumber"}>{item.value}</div>
                                                </div>
                                            ))
                                        }
                                    </div>
                                </div>}
                                {orderDelay.orderDelayData.delayedOrders.length>0 && <div className={"col-md-4"}>
                                    <div className={"delayDetails"}>
                                        <div className={"delayDetailsHead"}>Order delay by type</div>
                                        {
                                            this.getOrderTypeSplit(orderDelay.orderDelayData.delayedOrders).map((item, index) => (
                                                <div className={"orderStats"}>
                                                    <div className={"orderStatName"}>{item.name}</div>
                                                    <div className={"orderStatNumber"}>{item.value}</div>
                                                </div>
                                            ))
                                        }
                                    </div>
                                </div>}
                                <div className={"col-md-4"}>
                                    <div className={"delayDetails"}>
                                        <div className={"delayDetailsHead"}>Order delay predictions</div>
                                        <div className={"orderStats"}>
                                            <div className={"orderStatName"}>Next 15 minutes</div>
                                            <div className={"orderStatNumber"}>{this.getDelayPrediction(orderDelay.orderDelayData.ordersNotAck, 15)}</div>
                                        </div>
                                        <div className={"orderStats"}>
                                            <div className={"orderStatName"}>Next 30 minutes</div>
                                            <div className={"orderStatNumber"}>{this.getDelayPrediction(orderDelay.orderDelayData.ordersNotAck, 30)}</div>
                                        </div>
                                        <div className={"orderStats"}>
                                            <div className={"orderStatName"}>Next 1 hour</div>
                                            <div className={"orderStatNumber"}>{this.getDelayPrediction(orderDelay.orderDelayData.ordersNotAck, 60)}</div>
                                        </div>
                                    </div>
                                </div>
                                <br/>
                                {(orderDelay.orderDelayData.delayedOrders.length>0 || orderDelay.orderDelayData.ordersNotAck.length>0) && <div className={"links col-md-12"}>
                                    <ExcelFile element={<a className={"delayDownload"}>Click here</a> } filename={"impacted_orders"}>
                                        <ExcelSheet data={orderDelay.orderDelayData.delayedOrders} name="delayedOrders">
                                            <ExcelColumn label="EXTERNAL_ORDER_ID" value="EXTERNAL_ORDER_ID"/>
                                            <ExcelColumn label="GLOBAL_STORE_ID" value="GLOBAL_STORE_ID"/>
                                            <ExcelColumn label="EXTERNAL_ORDER_TYPE" value="EXTERNAL_ORDER_TYPE"/>
                                            <ExcelColumn label="CAP_ORDER_STATUS" value="CAP_ORDER_STATUS"/>
                                            <ExcelColumn label="PLANNED_TO_DATE" value="PLANNED_TO_DATE"/>
                                            <ExcelColumn label="PLANNED_FROM_DATE" value="PLANNED_FROM_DATE"/>
                                            <ExcelColumn label="TIMEZONE" value="TIMEZONE"/>
                                            <ExcelColumn label="ITEM_COUNT" value="ITEM_COUNT"/>
                                            <ExcelColumn label="CUTOFF_DATE" value="CUTOFF_DATE"/>
                                            <ExcelColumn label="CUTOFF_EXE_DATE" value="CUTOFF_EXE_DATE"/>
                                            <ExcelColumn label="DELIVERY_CUTOFF_EXE_DATE" value="DELIVERY_CUTOFF_EXE_DATE"/>
                                            <ExcelColumn label="ORDER_DROP_DATE" value="ORDER_DROP_DATE"/>
                                            <ExcelColumn label="DELAY" value="DELAY"/>
                                        </ExcelSheet>
                                        <ExcelSheet data={orderDelay.orderDelayData.ordersNotAck} name="unackOrders">
                                            <ExcelColumn label="EXTERNAL_ORDER_ID" value="EXTERNAL_ORDER_ID"/>
                                            <ExcelColumn label="GLOBAL_STORE_ID" value="GLOBAL_STORE_ID"/>
                                            <ExcelColumn label="EXTERNAL_ORDER_TYPE" value="EXTERNAL_ORDER_TYPE"/>
                                            <ExcelColumn label="CAP_ORDER_STATUS" value="CAP_ORDER_STATUS"/>
                                            <ExcelColumn label="PLANNED_TO_DATE" value="PLANNED_TO_DATE"/>
                                            <ExcelColumn label="PLANNED_FROM_DATE" value="PLANNED_FROM_DATE"/>
                                            <ExcelColumn label="TIMEZONE" value="TIMEZONE"/>
                                            <ExcelColumn label="ITEM_COUNT" value="ITEM_COUNT"/>
                                            <ExcelColumn label="CUTOFF_DATE" value="CUTOFF_DATE"/>
                                            <ExcelColumn label="CUTOFF_EXE_DATE" value="CUTOFF_EXE_DATE"/>
                                            <ExcelColumn label="DELIVERY_CUTOFF_EXE_DATE" value="DELIVERY_CUTOFF_EXE_DATE"/>
                                            <ExcelColumn label="OMS_STATUS" value="OMS_STATUS"/>
                                            <ExcelColumn label="IS_SLA_BREACHED" value="IS_SLA_BREACHED"/>
                                            <ExcelColumn label="TIME_REMAINING_FOR_SLA_BREACH" value="TIME_REMAINING_FOR_SLA_BREACH"/>
                                        </ExcelSheet>
                                    </ExcelFile>
                                    to download impacted orders.<br/>
                                    <a className={"delayDownload"} onClick={()=> this.sendDelayMail(orderDelay, auth)}>Click here</a>to email the report.<br/>
                                </div>}
                            </div>
                        </div>
                        }
                    </div>

                </div>
            </div>
        );
    }
}

OrderDelayMonitor.propTypes = {
    orderDelay:PropTypes.object,
    auth:PropTypes.object,
    getOrderDelay: PropTypes.func.isRequired,
    getUserInfo:PropTypes.func.isRequired,
    sendDelayEmail:PropTypes.func.isRequired
}

const mapStateToProps = state  => ({
    orderDelay:state.orderDelay,
    auth:state.auth
})

export default connect(mapStateToProps, { getOrderDelay, getUserInfo, sendDelayEmail })(withRouter(OrderDelayMonitor))

