import axios from 'axios';
import {
    BASE_URL,
    ORDER_DELAY_LOADING,
    GET_ERRORS,
    ORDER_DELAY_DATA,
    OAR_COMMS_LOADING,
    GET_COMMS_RECIPIENTS
} from './constants'

export const getOrderDelay = (body) => dispatch => {
    dispatch({
        type: ORDER_DELAY_LOADING,
        payload: {
            loading:true
        }
    });

    axios.post(BASE_URL + "grocery/getDelayedOrders", body)
        .then(response => {
            dispatch({
                type: ORDER_DELAY_DATA,
                payload: {
                    loading:false,
                    orderDelayData:response.data
                }
            });
        })
        .catch(error => {
            dispatch({
                type: GET_ERRORS,
                payload: error
            });
        })
}

export const sendDelayEmail = (body) => dispatch => {
    dispatch({
        type: ORDER_DELAY_LOADING,
        payload: {
            loading:true
        }
    });

    axios.post(BASE_URL + "grocery/getDelayedOrdersMail", body)
        .then(response => {
            console.log(response.data)
            if(response.data.message==="OK"){
                alert("Delay report sent to " + body.userEmail)
            }
            else{
                alert("Unexpected error occured while sending report to " + body.userEmail)
            }
            dispatch({
                type: ORDER_DELAY_LOADING,
                payload: {
                    loading:false
                }
            });
        })
        .catch(error => {
            dispatch({
                type: GET_ERRORS,
                payload: error
            });
        })
}

export const addOarCommsEmail = (name, email) => dispatch => {
    dispatch({
        type: OAR_COMMS_LOADING,
        payload: {
            commsLoading:true
        }
    });

    axios.post(BASE_URL + "addOarCommsEmail?name=" + name + "&value=" + email)
        .then(response => {
            console.log(response.data)
            if(response.data.message==="OK"){
                axios.get(BASE_URL + "getOarCommsEmails")
                    .then(response => {
                            dispatch({
                                type:GET_COMMS_RECIPIENTS,
                                payload:{
                                    commsLoading:false,
                                    emails:JSON.parse(response.data.message)
                                }
                            })
                    })
                    .catch(error => {
                        dispatch({
                            type: GET_ERRORS,
                            payload: error
                        });
                    })
            }
            else{
                alert("Unexpected error occured while adding recipient.")
            }
            dispatch({
                type: OAR_COMMS_LOADING,
                payload: {
                    commsLoading:false
                }
            });
        })
        .catch(error => {
            dispatch({
                type: GET_ERRORS,
                payload: error
            });
        })
}

export const getOarCommsEmails = () => dispatch => {
    dispatch({
        type:OAR_COMMS_LOADING,
        payload:{
            commsLoading:true,
        }
    })
    axios.get(BASE_URL + "getOarCommsEmails")
        .then(response => {
                dispatch({
                    type:GET_COMMS_RECIPIENTS,
                    payload:{
                        commsLoading:false,
                        emails:JSON.parse(response.data.message)
                    }
                })
        })
        .catch(error => {
            dispatch({
                type: OAR_COMMS_LOADING,
                payload: {
                    commsLoading:false
                }
            });
        })
}

export const sendCommsEmail = (formData) => dispatch => {
    dispatch({
        type:OAR_COMMS_LOADING,
        payload:{
            commsLoading:true,
        }
    })
    axios.post( BASE_URL + "sendOarComms", formData, {headers:{"Content-Type":"application/x-www-form-urlencoded"}})
        .then(response => {
            console.log(response)
            alert("Email communication sent.")
            dispatch({
                type:OAR_COMMS_LOADING,
                payload:{
                    commsLoading:false,
                }
            })
        })
        .catch(error => {
            dispatch({
                type: OAR_COMMS_LOADING,
                payload: {
                    commsLoading:false
                }
            });
        })
}