import axios from 'axios';
import {
    BASE_URL,
    SET_CUTOFFS,
    CUTOFF_LOADING,
    GET_ERRORS,
    SET_CUTOFF_ORDERS,
    SET_ORDERS_LOADING
} from './constants'
export const getCutoffs = (date) => dispatch => {
    dispatch({
        type: CUTOFF_LOADING,
        payload: {
            cutoffLoading:true
        }
    });
    axios.get(BASE_URL + "getCutoffDetails?date=" + date)
        .then(response => {
            
            let cutoffs = response.data.cutoffs;
            let cutoffSummary = {};
            for(let i=0; i<cutoffs.length; i++){
                cutoffs[i].collapse= false
                for(let j=0; j<cutoffs[i].orderGroups.length; j++){
                    if(cutoffSummary.hasOwnProperty(cutoffs[i].orderGroups.orderType))
                    cutoffSummary[cutoffs[i].orderGroups.orderType][cutoffs[i].orderGroups.staus] = cutoffs[i].orderGroups.count
                }
            }
            axios.get(BASE_URL + "getCutoffSummary?date=" + date)
                .then(summaryResponse => {
                    dispatch({
                        type: SET_CUTOFFS,
                        payload: {
                            cutoffs: cutoffs.sort(sortCutoffs),
                            summary:JSON.parse(summaryResponse.data.message),
                            cutoffLoading:false,
                            omsSummary:{},
                            omsDaySummaryRaw:'{}'
                        }
                    });
                })
                .catch(error => {
                    dispatch({
                        type: GET_ERRORS,
                        payload: error,
                        cutoffLoading:false
                    });
                })

        })
        .catch(error => {
            dispatch({
                type: GET_ERRORS,
                payload: error,
                cutoffLoading:false
            });
        })
        dispatch({
            type: SET_ORDERS_LOADING,
            payload: {
                cutoffOrdersLoading:true
            }
        });
        axios.get(BASE_URL + "getOmsSummary?date=" + date)
            .then(response => {
                let omsResponse =  JSON.parse(response.data.message)

                Object.keys(omsResponse).forEach(function(key) {
                    omsResponse[key] = parseStatuses(omsResponse[key])
                });

                dispatch({
                    type: SET_CUTOFF_ORDERS,
                    payload: {
                        omsSummary: omsResponse,
                        omsDaySummaryRaw:response.data.message,
                        cutoffOrdersLoading:false
                    }
                });
            })
            .catch(error => {
                dispatch({
                    type: GET_ERRORS,
                    payload: error,
                    cutoffOrdersLoading:false
                });
            })
}

export const collapseCutoff = (cutoffs, cutoffDate) => dispatch => {
    for(let i=0; i<cutoffs.cutoffs.length; i++){
        if(cutoffs.cutoffs[i].cutoffDate===cutoffDate){
            cutoffs.cutoffs[i].collapse = !cutoffs.cutoffs[i].collapse;
            break;
        }
    }
    dispatch({
        type: SET_CUTOFFS,
        payload: {
            cutoffs: cutoffs.cutoffs,
            twoDayCutoff:cutoffs.twoDayCutoff,
            summary:cutoffs.summary,
            omsSummary:cutoffs.omsSummary,
            cutoffLoading:false
        }
    });
}

export const setLoader = (loading) => dispatch => {
        dispatch({
            type: CUTOFF_LOADING,
            payload: {
                cutoffLoading:loading
            }
        })
}

function parseStatuses(data) {
    Object.keys(data).forEach(function(key) {
        let orderType = data[key];

        Object.keys(orderType).forEach(function(statusKey) {
            let orderStatus = orderType[statusKey];
            let draftOrders = 0;
            let createdOrders = 0;
            let acocOrders = 0;
            let poCretedOrders = 0;
            let poAckOrders = 0;
            let cancelled = 0;
            Object.keys(orderStatus).forEach(function(orderStatusKey) {
                if (parseFloat(orderStatusKey) === 1000) {
                    draftOrders += orderStatus[orderStatusKey]
                } else if (parseFloat(orderStatusKey) === 1100) {
                    createdOrders += orderStatus[orderStatusKey]
                } else if (parseFloat(orderStatusKey) > 1100 && parseFloat(orderStatusKey) < 2100.200) {
                    acocOrders += orderStatus[orderStatusKey]
                } else if (parseFloat(orderStatusKey) === 2100.200) {
                    poCretedOrders += orderStatus[orderStatusKey]
                } else if (parseFloat(orderStatusKey) >= 2100.300 && parseFloat(orderStatusKey) < 9000) {
                    poAckOrders += orderStatus[orderStatusKey]
                } else if (parseFloat(orderStatusKey) >= 9000) {
                    cancelled += orderStatus[orderStatusKey]
                }
            });
            orderType[statusKey] = {
                draftOrders: draftOrders,
                createdOrders: createdOrders,
                acocOrders: acocOrders,
                poCretedOrders: poCretedOrders,
                poAckOrders: poAckOrders,
                cancelled: cancelled
            }
        });
        data[key] = orderType
    });
    return data;
}


function sortCutoffs(a, b) {
    return new Date(a.cutoffDate).getTime() - new Date(b.cutoffDate).getTime();
}