import axios from 'axios';
import {BASE_URL, GET_ERRORS, SET_COMPONENTS, SET_ORGANIZATIONS, SET_ASSEMBLIES, SET_ENVIRONMENTS, DATA_LOADING, SET_PLATFORMS, TOGGLE_PLATFORM, UPDATE_COMPONENTS, PROCEDURES_RECEIVED} from './constants';

export const getOrganizations = () => dispatch => {
    dispatch({
        type: DATA_LOADING,
        payload: {
            loading:true
        }
    });
    axios.get(BASE_URL + "getOrganizations")
        .then(response => {
            dispatch({
                type: SET_ORGANIZATIONS,
                payload:{
                    organizations:JSON.parse(response.data.message),
                    loading:false
                }
            });
        })
        .catch(error => {
            dispatch({
                type: GET_ERRORS,
                payload: error,
                loading:false
            });
        })
}

