function updateComponentsFromResponse(pName, cId, procedureId, procedureState, procedureError, instanceId, actionState, updatedComponents) {
    for(let i=0; i<updatedComponents.length; i++){
        if(updatedComponents[i].platform===pName){
            console.log(updatedComponents[i])
            for(let j=0; j<updatedComponents[i].components.length; j++){

                if(updatedComponents[i].components[j].componentId===cId){
                    if(procedureId!=null) updatedComponents[i].components[j].procedureId = procedureId;
                    if(procedureState!=null) updatedComponents[i].components[j].procedureState = procedureState;
                    if(procedureError!=null) updatedComponents[i].components[j].procedureError = procedureError;
                    for(let k=0; k<updatedComponents[i].components[j].computes.instances.length; k++){
                        if(updatedComponents[i].components[j].computes.instances[k].ciId===instanceId){
                            updatedComponents[i].components[j].computes.instances[k].actionState = actionState;
                        }
                    }
                }
            }
        }
    }
    return updatedComponents;
}

