import {
    GET_ERRORS,
    SET_COMPONENTS,
    SET_ORGANIZATIONS,
    SET_ASSEMBLIES,
    SET_ENVIRONMENTS,
    DATA_LOADING,
    SET_PLATFORMS,
    TOGGLE_PLATFORM,
    UPDATE_COMPONENTS,
    TOGGLE_CHECKBOX,
    TRIGGER_RESTART,
    PROCEDURES_RECEIVED,
    ONEOPS_DATA_LOADING,
    ONEOPS_HEALTH
} from '../actions/constants';
const initialState = {
    components: []
};

export default function(state = initialState, action) {
    switch(action.type) {
        case GET_ERRORS:
            return action.payload;
        case SET_COMPONENTS:
            return {
                ...state,
                components:action.payload.components,
                loading:action.payload.loading,
                selectDfw: action.payload.selectDfw,
                selectCdc: action.payload.selectCdc,
                selectAzure: action.payload.selectAzure,
                selectPrimary: action.payload.selectPrimary,
                selectSecondary: action.payload.selectSecondary
            };
        case UPDATE_COMPONENTS:
            return {
                ...state,
                components: action.payload.oneops.components,
                selectDfw: action.payload.oneops.selectDfw,
                selectCdc: action.payload.oneops.selectCdc,
                selectAzure: action.payload.oneops.selectAzure,
                selectPrimary: action.payload.oneops.selectPrimary,
                selectSecondary: action.payload.oneops.selectSecondary
            }
        case SET_ORGANIZATIONS:
            return {
                ...state,
                organizations:action.payload.organizations,
                loading:action.payload.loading,
                selectedOrg: null,
                assemblies:[],
                selectedAssembly: null,
                environments:[],
                selectedEnv:null,
                platforms:[],
                selectedPlatforms:null,
                components:[]
            };
        case SET_ASSEMBLIES:
            return {
                ...state,
                assemblies:action.payload.assemblies,
                selectedOrg: action.payload.selectedOrg,
                loading:action.payload.loading,
                selectedAssembly:null,
                environments: [],
                selectedEnv:null,
                platforms: [],
                selectedPlatforms:null,
                components:[]

            };
        case SET_ENVIRONMENTS:
            return {
                ...state,
                environments:action.payload.environments,
                selectedAssembly: action.payload.selectedAssembly,
                loading:action.payload.loading,
                selectedEnv:null,
                platforms: [],
                selectedPlatforms:null,
                components:[]
            };
        case SET_PLATFORMS:
            return {
                ...state,
                platforms: action.payload.platforms,
                selectedEnv: action.payload.selectedEnv,
                loading: action.payload.loading,
                components:[]
            }
        case DATA_LOADING:
            return {
                ...state,
                loading:action.payload.loading
            };
        case TOGGLE_PLATFORM:
            return {
                ...state,
                components: action.payload.components
            };
        case TOGGLE_CHECKBOX:
            return {
                ...state,
                components: action.payload.components
            };
        case TRIGGER_RESTART:
            return {
                ...state,
                restartRequestBody: action.payload.requestData
            };
        case PROCEDURES_RECEIVED:
            return {
                ...state,
                loading: action.payload.loading,
                procedures: action.payload.procedures
            }
        case ONEOPS_DATA_LOADING:
            return {
                ...state,
                oneopsHealthLoading: action.payload.oneopsHealthLoading
            }
        case ONEOPS_HEALTH:
            return {
                ...state,
                health:action.payload.health,
                oneopsHealthLoading:action.payload.oneopsHealthLoading
            }
        default:
            return state;
    }
}