import {
    DEV_LOGOUT_URL,
    GET_ERRORS,
    REMOVE_USER,
    SET_CURRENT_USER,
    GET_CURRENT_USER,
    SET_USER_ACCESS,
    SET_ACCESS_REQUEST_LOADING
} from '../actions/constants';
import isEmpty from '../helpers/is-empty'
const initialState = {
    isAuthenticated: false,
    accessRequestLoading:false,
    user: {},
    userAccess:[]
};

export default function(state = initialState, action) {
    switch(action.type) {
        case GET_ERRORS:
            return action.payload;
        case SET_CURRENT_USER:
            return {
                ...state,
                isAuthenticated: !isEmpty(action.payload),
                user: action.payload
            };
        case GET_CURRENT_USER:
            return {
                ...state,
                user:action.payload
            };
        case REMOVE_USER:
            window.location = DEV_LOGOUT_URL;
            return {
                ...state,
                isAuthenticated: false,
                user:{}
            };
        case SET_USER_ACCESS:
            return {
                ...state,
                userAccess:action.payload
            };
        case SET_ACCESS_REQUEST_LOADING:
            return {
                ...state,
                accessRequestLoading: action.payload
            }
        default:
            return state;
    }
}