import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import { withRouter } from 'react-router-dom';
import Navbar from "../../components/Navbar/Navbar";
import { getUserInfo } from '../../actions/auth-actions'
import { getHealth, toggleSystem } from '../../actions/oneops-health-actions'
import CustomLoader from "../App/CustomLoader";
import { Collapse } from 'reactstrap';
import OneOpsHealth from '../../data/sctOneOpsHealth.json';
import './OneOpsHealthCheck.css'

class OneOpsHealthCheck extends Component {

    constructor(props){
        super(props);
        this.props.getHealth();
        this.state={
            allAppDetails: OneOpsHealth
        }
    }

    componentDidMount() {
        this.props.getUserInfo();
        setInterval(this.getHealth.bind(this), 5000*60);
    }

    getHealth(){
        this.props.getHealth();
    }

    mapComponents(appDetails){
        return(
            <div className={"systemDetails"}>
                <div className={"row platformRowHead"}>
                    <div className={"col-md-5 platformCol"}>PLATFORM</div>
                    <div className={"col-md-3 platformCol"}>LB Health</div>
                    <div className={"col-md-4 platformCol"}>VM HEALTH</div>
                </div>
                {
                    appDetails.map((item, index) => (
                        <div className={"row platformRow"} style={{backgroundColor:this.getRowColor(item)}}>
                            <div className={"col-md-6 platformCol"}>{item.platformName.toUpperCase()}</div>
                            <div className={"col-md-3 platformCol"}>{item.lbHealth + "%"}</div>
                            <div className={"col-md-3 platformCol"}>{item.vmHealth + "%"}</div>
                        </div>
                    ))
                }
            </div>
        )
    }
    getAverageHealth(appDetails){
        let totalLB = 0, totalVM = 0;
        for(let i=0; i<appDetails.length; i++){
            totalLB+= appDetails[i].lbHealth
            totalVM+=appDetails[i].vmHealth
        }
        let avg = (totalLB + totalVM)/(appDetails.length*2);
        if(avg===100) return "#3abb3a";
        else if(avg<100 && avg>80) return "#ffa400";
        else return "#dc2121"
    }

    getRowColor(item){
        let total = (item.lbHealth + item.vmHealth)/2;
        if(total===100) return "#3abb3a";
        if(total<100 && total>80) return "#ffa400";
        else return "#dc2121"
    }


    render() {
        let { oneops } = this.props
        return (
            <div>
                {oneops.oneopsHealthLoading && <CustomLoader/>}
                <Navbar/>
                <div className="jumbotron" style={{minHeight:'100vh'}}>
                    <h1>Oneops Health Check</h1>
                    <ul className="breadcrumb">
                        <li><Link to="home">Home</Link></li>
                        <li>Oneops Health Check</li>
                    </ul>
                    <hr size="10"/>
                    <div className="row">
                        {
                            oneops.health && oneops.health.map((appItem, index) => (
                                <div key={index} className={"col-md-3"}>
                                    <div className={"col-md-12"} >
                                        <div onClick={() => this.props.toggleSystem(oneops.health, appItem.appName)} className={"systemButton"}>{appItem.appName.toUpperCase()} <div style={{backgroundColor:this.getAverageHealth(appItem.appDetails)}} className={"statusIcon"}></div></div>
                                        <Collapse isOpen={appItem.expand} style={{width:'100%'}}>
                                            {this.mapComponents(appItem.appDetails)}
                                        </Collapse>
                                    </div>
                                </div>
                            ))
                        }
                    </div>
                </div>
            </div>
        );
    }
}

OneOpsHealthCheck.propTypes = {
    oneops:PropTypes.object,
    getHealth: PropTypes.func.isRequired,
    toggleSystem:PropTypes.func.isRequired,
    getUserInfo:PropTypes.func.isRequired
}

const mapStateToProps = state  => ({
    oneops:state.oneops
})

export default connect(mapStateToProps, { getHealth, getUserInfo, toggleSystem})(withRouter(OneOpsHealthCheck))


