import React, { Component } from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import Computes from '../Computes/Computes'

import { PieChart, Pie, Cell} from 'recharts';
import './ToggleListItem.css'
class ToggleListItem extends Component {

    getPieData(computes){
        let colors = {
            "good": "#3cb474",
            "notify":"#f6c243",
            "unhealthy":"#da3636"
        }
        let totalInstances = 0;
        let goodInstances = 0;
        for(let i=0; i<computes.instances.length; i++){
                totalInstances++;
                if(computes.instances[i].opsState==='good'){
                    goodInstances++;
                }
        }
        return [
            {
                name: 'good',
                value: parseInt((goodInstances/totalInstances)*100).toFixed(0),
                color: colors["good"]
            },
            {
                name: 'unhealthy',
                value: parseInt(((totalInstances-goodInstances)/totalInstances)*100).toFixed(0),
                color: parseInt(((totalInstances-goodInstances)/totalInstances)*100)<50?colors["notify"]:colors["unhealthy"]
            }]
    }

    getGoodPct(data){
        for(let i=0; i<data.length; i++){
            if(data[i].name==="good") return data[i].value
        }
    }

    render() {
        return(
            <div className={"row"}>
                {
                    this.props.components.map((item, index) => {
                        let data = this.getPieData(item.computes);
                        for(let i=0; i<data.length;i++){
                            data[i].value  = parseInt(data[i].value)
                        }
                        return (<div style={{textAlign:'center', padding:'0.3rem'}} className={"col-md-3"}>
                            <div style={{padding:'1rem', backgroundColor:'#efefef'}}>
                                <div className={"row"}>
                                    <div className={"col-md-4"}>

                                        <PieChart width={800} height={100} onMouseEnter={this.onPieEnter}>
                                            <Pie
                                                data={data}
                                                cx={50}
                                                cy={45}
                                                innerRadius={35}
                                                outerRadius={48}
                                                fill="#8884d8"
                                                paddingAngle={0}
                                                dataKey="value"
                                            >
                                                {
                                                    data.map((entry, index) => <Cell key={`cell-${index}`} fill={entry.color} />)
                                                }
                                            </Pie>
                                        </PieChart>
                                        <div className={"goodPctText"}>{this.getGoodPct(data) + "%"}</div>
                                        <div style={{fontSize:'0.8rem', marginTop:'0.5rem'}}>{item.componentName}</div>
                                        <div style={{fontSize:'0.6rem', marginTop:'0.1rem', fontWeight:'bold', color:'black'}}>{item.description}</div>
                                    </div>
                                    <div className={"col-md-8"}>
                                        <Computes computes={item.computes.instances} component={item.componentId} platform={this.props.platform} description={item.description} applications={this.props.applications}/>
                                    </div>
                                </div>
                            </div>
                        </div>)
                    }

                    )
                }
            </div>
        )
    }
}
ToggleListItem.propTypes = {
}

const mapStateToProps = state  => ({
})

export default connect(mapStateToProps,{  })(withRouter(ToggleListItem))