import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { withRouter } from 'react-router-dom';
import { handleAuthentication, getUserAccess } from '../../actions/auth-actions';
import Dashboard  from "../Dashboard";
import CustomLoader from "./CustomLoader";
class App extends Component {

  componentDidMount() {
    if(!this.props.auth.isAuthenticated){
      this.props.handleAuthentication();
      this.props.getUserAccess();
    }
    else{
      this.props.getUserAccess();
    }
  }

  componentWillReceiveProps(nextProps) {
    if(nextProps.auth) {
      window.history.pushState({}, document.title, "/");
    }
  }

  render() {
    let { auth } = this.props
    return (
        <div>
        {auth.isAuthenticated?<Dashboard/>:<CustomLoader/>}
        </div>
    );
  }
}

App.propTypes = {
  handleAuthentication: PropTypes.func.isRequired,
  auth: PropTypes.object,
  getUserAccess: PropTypes.func.isRequired
}

const mapStateToProps = state  => ({
  errors:state.errors,
  auth:state.auth
})

export default connect(mapStateToProps,{ handleAuthentication, getUserAccess })(withRouter(App))

