import axios from 'axios';
import {
    GET_ERRORS,
    REMOVE_USER,
    SET_CURRENT_USER,
    PROD_LOGIN_URL,
    DEV_LOGIN_URL,
    DEV_REDIRECT_URL,
    BASE_URL,
    SET_USER_ACCESS,
    GET_CURRENT_USER,
    PROFILE,
    PROD_REDIRECT_URL,
    SET_ACCESS_REQUEST_LOADING, DEV_LOGOUT_URL, PROD_LOGOUT_URL
} from './constants'

export const handleAuthentication = () => dispatch => {
    let vars = {};
    window.location.href.replace(/[?&]+([^=&]+)=([^&]*)/gi, function(m, key, value) {
        vars[key] = value;
    });
    if (!vars["code"]) {
        if(PROFILE==="DEV"){
            window.location = DEV_LOGIN_URL;
        }
        else{
            window.location = PROD_LOGIN_URL;
        }
    }
    else{
        let body = {
                grantType: "authorization_code",
                code: vars["code"],
                redirectURL: PROFILE==="DEV"?DEV_REDIRECT_URL:PROD_REDIRECT_URL,
        }
        let tokenRoute = PROFILE==="DEV"?"getNirvanaDevToken":"getNirvanaToken";
        axios.post(BASE_URL + tokenRoute, body)
            .then(response => {
                localStorage.setItem('nirvanaUsername', response.data.name);
                localStorage.setItem('nirvanaIsLoggedIn', response.data.isValid);
                localStorage.setItem('nirvanaEmail', response.data.email);
                localStorage.setItem('nirvanaUserID', response.data.userId);
                dispatch({
                    type: SET_CURRENT_USER,
                    payload: response.data
                });
            })
            .catch(error => {
                dispatch({
                    type: GET_ERRORS,
                    payload: error
                });
            })
    }
}

export const getUserInfo = () => dispatch => {
    let user = {
        username: localStorage.getItem('nirvanaUsername'),
        isLoggedIn: localStorage.getItem('nirvanaIsLoggedIn'),
        email: localStorage.getItem('nirvanaEmail'),
        userId: localStorage.getItem('nirvanaUserID')
    }
    dispatch({
        type: GET_CURRENT_USER,
        payload: user
    });
}

export const getUserAccess = () => dispatch => {
    axios.get(BASE_URL + "getUserAccess/" + localStorage.getItem("nirvanaUserID"))
        .then(response => {
            localStorage.setItem('nirvanaUserAccess', response.data);
            dispatch({
                type: SET_USER_ACCESS,
                payload: response.data.message
            });
        })
        .catch(error => {
            dispatch({
                type: GET_ERRORS,
                payload: error
            });
        })
}

export const addUserAccess = (userDetails) => dispatch => {
    dispatch({
        type: SET_ACCESS_REQUEST_LOADING,
        payload: true
    });
    axios.post(BASE_URL + "addUserAccess", userDetails)
        .then(response => {
            localStorage.setItem('nirvanaUserAccess', response.data);
            dispatch({
                type: SET_ACCESS_REQUEST_LOADING,
                payload: false
            });
            alert("Access request successful!")
        })
        .catch(error => {
            dispatch({
                type: SET_ACCESS_REQUEST_LOADING,
                payload: false
            });
        })
}

export const logout = () => dispatch => {
    localStorage.removeItem('nirvanaIsLoggedIn');
    localStorage.removeItem('nirvanaUsername');
    localStorage.removeItem('nirvanaEmail');
    localStorage.removeItem('nirvanaUserID');
    dispatch({
        type: REMOVE_USER,
        payload: null
    });
    if(PROFILE==="DEV"){
        window.location = DEV_LOGOUT_URL;
    }
    else{
        window.location = PROD_LOGOUT_URL;
    }
}
