import {
    SET_CUTOFFS,
    CUTOFF_LOADING,
    SET_CUTOFF_ORDERS,
    SET_ORDERS_LOADING
} from '../actions/constants';

const initialState = {

};
export default function(state = initialState, action) {
    switch(action.type) {
        case SET_CUTOFFS:
            return {
                ...state,
                cutoffLoading:action.payload.loading,
                cutoffs: action.payload.cutoffs,
                summary:action.payload.summary,
                cutoffOrders:[["ORDER_ID", "STORE_ID", "ORDER_DATE", "CUTOFF_EXEC_DATE", "IS_ROVR_CUTOFF_EXEC", "OMS_STATUS"]],
                omsSummary:action.payload.omsSummary,
                omsDaySummaryRaw:action.payload.omsDaySummaryRaw
            };
        case CUTOFF_LOADING:
            return {
                ...state,
                cutoffLoading:action.payload.cutoffLoading
            };
        case SET_ORDERS_LOADING:
            return {
                ...state,
                cutoffOrdersLoading:action.payload.cutoffOrdersLoading
            }
        case SET_CUTOFF_ORDERS:
            return {
                ...state,
                omsSummary:action.payload.omsSummary,
                omsDaySummaryRaw:action.payload.omsDaySummaryRaw,
                cutoffOrdersLoading:action.payload.cutoffOrdersLoading
            }
        default:
            return state;
    }
}