import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { withRouter } from 'react-router-dom';
import { toggleCheckbox } from '../../actions/oneops-actions';
import {ic_done} from 'react-icons-kit/md/ic_done'
import {ic_query_builder} from 'react-icons-kit/md/ic_query_builder'
import {ic_error} from 'react-icons-kit/md/ic_error'

import { Icon } from 'react-icons-kit';
class Computes extends Component {
    constructor(props){
        super(props);
        this.state ={

        }
    }

    checkApplicationStatus(applications, description, ip){
        if(ip){
            for(let i=0; i<applications.length; i++){
                if(applications[i].hasOwnProperty("ip")){
                    if(applications[i].ip===ip){
                        if(applications[i].applicationNames.includes(description)){
                            return true;
                        }
                    }
                }
            }
        }
    }

    render() {
        let { oneops  } = this.props
        return(
            <div style={{textAlign:'left', justifyContent:'left'}}>
                {
                    this.props.computes.map((item, index) => (
                        <div key={index}>
                            <div className="checkbox" style={{ textAlign:'left', height:'1.2rem'}}>
                                <label style={{fontSize:'0.6rem', marginBottom:0, paddingBottom:0}}>
                                    <input
                                        type="checkbox"
                                        checked={item.visible}
                                        onClick={() => this.props.toggleCheckbox(oneops, this.props.component, this.props.platform, item)}
                                    />
                                    &nbsp;
                                    <div style={{width:'7px', height:'7px', backgroundColor:item.opsState==='good'?'#3cb474':(item.opsState==='unhealthy'?'#da3636':'#f6c243'), borderRadius:'7px', display:'inline-block'}}></div>
                                    &nbsp;
                                    <span style={{fontWeight:item.priority==='1'?'bold':'light', color:'black'}}>{item.cloudName?item.cloudName:""} ({item.publicIp?item.publicIp:""})</span> &nbsp;&nbsp;&nbsp;
                                    {(item.actionState==="pending" || item.actionState==="inprogress") && <Icon style={{display: "inline-block", backgroundColor: "transparent", width:"15px", height:"15px", borderRadius:"7.5px", color: 'black', position:'absolute', right:'35px'}} icon={ic_query_builder} size={16}/>}
                                    {this.checkApplicationStatus(this.props.applications, this.props.description, item.publicIp) && <Icon style={{display: "inline-block", backgroundColor: "green", width:"14px", height:"14px", borderRadius:"7px", color: '#ffffff', position:'absolute', right:'15px'}} icon={ic_done} size={14}/>}
                                    {!this.checkApplicationStatus(this.props.applications, this.props.description, item.publicIp) && <Icon style={{display: "inline-block", backgroundColor: "transparent", width:"14px", height:"15px", borderRadius:"7.5px", color: '#d3d3d3', position:'absolute', right:'16px'}} icon={ic_error} size={16}/>}
                                </label>
                            </div>

                        </div>
                    ))
                }
            </div>
        )
    }
}
Computes.propTypes = {
    toggleCheckbox: PropTypes.func.isRequired,
    oneops:PropTypes.object
}

const mapStateToProps = state  => ({
    oneops: state.oneops,
})

export default connect(mapStateToProps,{ toggleCheckbox })(withRouter(Computes))