import axios from 'axios';
import {BASE_URL, DATA_LOADING, GET_ERRORS, GET_PO_CHECKLIST, TOGGLE_CHECKLIST_ITEM} from './constants';

export const getPoChecklist = () => dispatch => {
    dispatch({
        type: DATA_LOADING,
        payload: {
            loading:true
        }
    });
    axios.get(BASE_URL + "validatePoCreation")
        .then(response => {
            dispatch({
                type: GET_PO_CHECKLIST,
                payload: {
                    loading:false,
                    poCheckList:response.data
                }
            });
        })
        .catch(error => {
            dispatch({
                type: GET_ERRORS,
                payload: error
            });
        })
}

export const toggleCategory = (poCheckList, dataType) => dispatch => {
    if(dataType==="RFR") poCheckList.RFR.collapse = !poCheckList.RFR.collapse
    if(dataType==="FRS") poCheckList.FRS.collapse = !poCheckList.FRS.collapse
    if(dataType==="ACOC") poCheckList.ACOC.collapse = !poCheckList.ACOC.collapse
    if(dataType==="RACOC") poCheckList.RACOC.collapse = !poCheckList.RACOC.collapse
    dispatch({
        type: TOGGLE_CHECKLIST_ITEM,
        payload: {
            loading:false,
            poCheckList:poCheckList
        }
    });
}
