import React from 'react';
import { HashRouter as Router, Route } from "react-router-dom";

import { Provider } from 'react-redux';
import store from './store'

import Home from './containers/App/App';
import Logout from "./containers/Logout";
import HealthMonitor from './containers/HealthMonitor/HealthMonitor';
import POChecklist from './containers/POChecklist/POChecklist';
import ReportsManager from './containers/ReportsManager/ReportsManager';
import CutoffMonitor from "./containers/CutoffMonitor/CutoffMonitor";
import OneOpsHealthCheck from './containers/OneOpsHealthCheck/OneOpsHealthCheck';
import ManageAccess from './containers/ManageAccess/ManageAccess'
import ViewReport from './containers/ReportsManager/ViewReport/ViewReport'
import OrderDelayMonitor from './containers/OrderDelayMonitor/OrderDelayMonitor'
import OarComms from './containers/OarComms/OarComms'
import 'bootstrap/dist/css/bootstrap.min.css';
import './App.css'
import {PrivateRoute} from "./HOC/private-route";
function App() {
  return (
      <Provider store = { store }>
          <Router>
              <Route exact path="/" component={Home} />
              <Route exact path="/home" component={Home} />
              <Route exact path="/logout" component={Logout} />
              <PrivateRoute exact path="/healthMonitor" component={HealthMonitor}/>
              <PrivateRoute exact path="/poChecklist" component={POChecklist}/>
              <PrivateRoute exact path="/reports" component={ReportsManager}/>
              <PrivateRoute exact path="/cutoffMonitor" component={CutoffMonitor}/>
              <PrivateRoute exact path="/oneOpsHealthCheck" component={OneOpsHealthCheck}/>
              <PrivateRoute exact path="/manageAccess" component={ManageAccess}/>
              <PrivateRoute exact path="/report/:reportName" component={ViewReport}/>
              <PrivateRoute exact path="/orderDelay" component={OrderDelayMonitor}/>
              <PrivateRoute exact path="/oarComms" component={OarComms}/>
          </Router>
      </Provider>
  );
}

export default App;
