import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import { withRouter } from 'react-router-dom';
import Navbar from "../../components/Navbar/Navbar";
import { getUserInfo, getUserAccess } from '../../actions/auth-actions';
import { getOrganizations, getAssemblies, getEnvironments, getPlatforms, getComponents, collapsePlatform, triggerRestart } from '../../actions/oneops-actions';
import Select from 'react-select';
import CustomLoader from "../App/CustomLoader";
import ToggleList from '../../components/ToggleList/ToggleList'
import { confirmAlert } from 'react-confirm-alert'; // Import
import 'react-confirm-alert/src/react-confirm-alert.css' // Import css
class HealthMonitor extends Component {

    constructor(props){
        super(props);
        this.state={
            selectedAll:false,
            collapse: false,
            availableActions:[
                {
                    "label": "restart",
                    "value": "restart"
                },
                {
                    "label": "start",
                    "value": "start"
                },
                {
                    "label": "stop",
                    "value": "stop"
                }
            ],
            selectedPlatforms:[]
        }
    }

    componentDidMount() {
        this.props.getOrganizations();
        this.props.getUserInfo();
        this.props.getUserAccess();
        this.setState({
            selectedPlatforms:null,
            selectedAction:null,
        })
    }

    updatePlatforms = selected => {
        this.setState({selectedPlatforms: selected})
    };

    updateSelectedAction = selected => {
        this.setState({selectedAction: selected})
    }

    selectAll = () => {
        this.setState({selectedAll:!this.state.selectedAll, selectedPlatforms:null})
    }
    submitAction = (oneops, user) => {
        if(!this.state.selectedAction){
            alert("Please select an action.")
        } else{
            confirmAlert({
                title: 'Confirm ' + this.state.selectedAction.value,
                message: 'Are you sure to perform the action?',
                buttons: [
                    {
                        label: 'Yes',
                        onClick: () =>  this.props.triggerRestart(oneops, user, this.state.selectedAction.value)
                    },
                    {
                        label: 'No',
                        onClick: () => {}
                    }
                ]
            })
        }

    }

    submit = () => {
        if(!this.props.oneops.selectedOrg || !this.props.oneops.selectedAssembly || !this.props.oneops.selectedEnv){
            alert("Please select organisation, assembly, environment and platforms.")
        }
        else{
            if(!this.state.selectedAll && !this.state.selectedPlatforms){
                alert("Please select organisation, assembly, environment and platforms.")
            }
            else {
                let body = {
                    org: this.props.oneops.selectedOrg.value,
                    assembly: this.props.oneops.selectedAssembly.value,
                    env: this.props.oneops.selectedEnv.value
                }
                let platformList = [];
                if(this.state.selectedAll){
                    for(let i=0; i<this.props.oneops.platforms.length; i++){
                        platformList.push(this.props.oneops.platforms[i].value);
                    }
                    body.platforms = platformList;
                } else{
                    for(let i=0; i<this.state.selectedPlatforms.length; i++){
                        platformList.push(this.state.selectedPlatforms[i].value);
                    }
                    body.platforms = platformList;
                }
                this.props.getComponents(body)
            }
        }
    }

    render() {
        let { oneops, auth  } = this.props
        console.log(auth.userAccess)
        return (
            <div>
                {oneops.loading && <CustomLoader/>}
                <Navbar/>
                <div className="jumbotron" style={{minHeight:'100vh'}}>
                    <h1>OneOps Health Monitor</h1>
                    <ul className="breadcrumb">
                        <li><Link to="home">Home</Link></li>
                        <li>OneOps Health Monitor</li>
                    </ul>
                    <hr size="30"/>
                    <div className="row">
                        <div className="col-md-2">
                            <label>Select Organization</label>
                            <Select
                                value={oneops.selectedOrg}
                                onChange={org => {
                                    this.props.getAssemblies(org)
                                    this.setState({selectedPlatforms:[]})
                                }}
                                options={oneops.organizations}
                                placeholder="Select org.."
                            />
                        </div>
                        <div className="col-md-2">
                            <label>Select Assembly</label>
                            <Select
                                value={oneops.selectedAssembly}
                                onChange={assembly => {
                                    this.props.getEnvironments(oneops.selectedOrg, assembly)
                                    this.setState({selectedPlatforms:[]})
                                }}
                                options={oneops.assemblies}
                                placeholder="Select assembly.."
                            />
                        </div>
                        <div className="col-md-2">
                            <label>Select Environment</label>
                            <Select
                                value={oneops.selectedEnv}
                                onChange={env => {
                                    this.props.getPlatforms(oneops.selectedOrg, oneops.selectedAssembly, env)
                                    this.setState({selectedPlatforms:[]})
                                }}
                                options={oneops.environments}
                                placeholder="Select environment.."
                            />
                        </div>
                        <div className="col-md-3">
                            <label>Select Platform</label>
                            <Select
                                value={this.state.selectedPlatforms}
                                onChange={this.updatePlatforms}
                                options={oneops.platforms}
                                placeholder="Select platform.."
                                isMulti={true}
                                isDisabled={this.state.selectedAll}
                            />
                            <div className="checkbox" style={{paddingTop:'3px', fontSize:'0.8rem'}}>
                                <label><input type="checkbox" checked={this.state.selectedAll} onChange={this.selectAll}/>&nbsp;&nbsp;Select All</label>
                            </div>
                        </div>
                        <div className="col-md-1" style={{marginTop:"2rem"}}>
                            <button type="button" className="btn btn-default" style={{"backgroundColor":"#35b96f", "color":"white", "width":"100%", outline:'none'}} onClick={this.submit}>Go Go Go!</button>
                        </div>
                        {oneops.components.length>0 && auth.userAccess.includes("ONEOPS_ACTION_RW") && <div className="col-md-1">
                            <label>Select Action</label>
                            <Select
                                value={this.state.selectedAction}
                                onChange={this.updateSelectedAction}
                                options={this.state.availableActions}
                                isMulti={false}
                            />
                        </div>}
                        {oneops.components.length>0 && auth.userAccess.includes("ONEOPS_ACTION_RW") &&
                        <div className="col-md-1" style={{marginTop:"2rem"}}>
                            <button type="button" className="btn btn-danger" style={{"backgroundColor":"#c32424", "color":"white", "width":"100%", outline:'none'}} onClick={() => this.submitAction(oneops, auth.user.userId)}>Submit</button>
                        </div>}
                    </div>
                    <ToggleList/>

                </div>
            </div>
        );
    }
}

HealthMonitor.propTypes = {
    auth: PropTypes.object,
    oneops: PropTypes.object,
    getOrganizations: PropTypes.func.isRequired,
    getAssemblies: PropTypes.func.isRequired,
    getEnvironments: PropTypes.func.isRequired,
    getPlatforms: PropTypes.func.isRequired,
    getComponents: PropTypes.func.isRequired,
    collapsePlatform: PropTypes.func.isRequired,
    getUserInfo:PropTypes.func.isRequired,
    triggerRestart:PropTypes.func.isRequired,
    getUserAccess: PropTypes.func.isRequired
}

const mapStateToProps = state  => ({
    auth:state.auth,
    oneops: state.oneops,
})

export default connect(mapStateToProps, { getOrganizations, getAssemblies, getEnvironments, getPlatforms, getComponents, collapsePlatform, getUserInfo, triggerRestart, getUserAccess })(withRouter(HealthMonitor))

