import { combineReducers } from 'redux';
import authReducer from './auth-reducer';
import oneopsReducer from './oneops-reducer';
import poCheckListReducer from './po-checklist-reducer';
import reportsmanagerReducer from './oar-reports-reducer';
import cutoffMonitor from './cutoff-monitor-reducer'
import orderDelayMonitor from './order-delay-reducer'
export default combineReducers({
    auth: authReducer,
    oneops: oneopsReducer,
    poCheckList: poCheckListReducer,
    reports: reportsmanagerReducer,
    cutoffs:cutoffMonitor,
    orderDelay: orderDelayMonitor
});