import {
    GET_ERRORS,
    ORDER_DELAY_LOADING,
    ORDER_DELAY_DATA,
    GET_COMMS_RECIPIENTS,
    OAR_COMMS_LOADING
} from '../actions/constants';
const initialState = {
    commsLoading:false
};

export default function(state = initialState, action) {
    switch(action.type) {
        case ORDER_DELAY_DATA:
            return {
                ...state,
                orderDelayData:action.payload.orderDelayData,
                loading:action.payload.loading
            };
        case ORDER_DELAY_LOADING:
            return {
                ...state,
                loading:action.payload.loading
            };
        case GET_ERRORS:
            return {
                ...state,
                cutoffOrdersLoading:action.payload.cutoffOrdersLoading,
                loading:action.payload.loading
            }
        case OAR_COMMS_LOADING:
            return {
                ...state,
                commsLoading:action.payload.commsLoading
            }
        case GET_COMMS_RECIPIENTS:
            return {
                ...state,
                commsLoading:action.payload.commsLoading,
                emails: action.payload.emails
            }

        default:
            return state;
    }
}