import React, { Component } from 'react';

import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { withRouter } from 'react-router-dom';
import { setLoader } from '../../actions/cutoff-monitor-actions'

import { CSVLink } from "react-csv";
import axios from "axios";
import {BASE_URL} from "../../actions/constants";
import Loader from "../../containers/App/CustomLoader";
import moment from 'moment';
import './CSVDownloader.css'
class CSVDownloader extends Component {
    csvLink = React.createRef()
    state = { data: [], loading:false }

    fetchData = () => {
        this.props.setLoader(true)
        this.getData(this.props.cutoffDate, this.props.orderType, this.props.cutoffStatus).then(data => {
            console.log(data)
            this.setState({ data:data }, () => {
                
                this.csvLink.current.link.click()
                this.props.setLoader(false)
            })
        })
    }

    getData = (cutoffDate, orderType, cutoffStatus) => {
        return new Promise(function(resolve, reject) {
            axios.get(BASE_URL + "getRovrOmsCutoffStatus?cutoffDate=" + cutoffDate + "&orderType=" + orderType + "&cutoffStatus=" + cutoffStatus)
                .then(cutoffOrders => {
                    let data = [];
                    let temp  = [];
                    temp.push("ORDER_ID")
                    temp.push("STORE_ID")
                    temp.push("ORDER_DATE")
                    temp.push("CUTOFF_EXEC_DATE")
                    temp.push("IS_ROVR_CUTOFF_EXEC")
                    temp.push("OMS_STATUS")
                    data.push(temp)
                    for(let i=0; i<cutoffOrders.data.length; i++){
                        let temp  = [];
                        temp.push(cutoffOrders.data[i].orderId)
                        temp.push(cutoffOrders.data[i].storeId)
                        temp.push(cutoffOrders.data[i].orderDate)
                        cutoffStatus==="CUTOFF_EXECUTED"?temp.push(cutoffOrders.data[i].cutoffExecDate):temp.push("NA")
                        orderType==="DELIVERY"?temp.push(cutoffOrders.data[i].isRovrCutoffTriggered):temp.push("NA")
                        temp.push(cutoffOrders.data[i].omsStatus)
                        data.push(temp)
                    }
                    resolve(data)
                })
                .catch(error => {
                    reject(error)
                })
        })
    }

    getOmsSummary(){
        if(this.props.omsSummary){
            if(this.props.omsSummary.hasOwnProperty(this.props.orderType)){
                let typeSummary = this.props.omsSummary[this.props.orderType];
                let statusSummary = typeSummary[this.props.cutoffStatus]
                if(statusSummary){
                    let total = statusSummary.draftOrders + statusSummary.createdOrders + statusSummary.acocOrders + statusSummary.poCretedOrders + statusSummary.poAckOrders + statusSummary.cancelled
                    return (
                        <div>
                            <div className={"orderStatusLine"} onClick={this.fetchData}>
                                {statusSummary.draftOrders>0 && <div style={{width:(statusSummary.draftOrders/total)*100+"%"}} className={"draftOrders"}></div>}
                                {statusSummary.createdOrders>0 && <div style={{width:(statusSummary.createdOrders/total)*100+"%"}} className={"createdOrders"}></div>}
                                {statusSummary.acocOrders>0 && <div style={{width:(statusSummary.acocOrders/total)*100+"%"}} className={"acocOrders"}></div>}
                                {statusSummary.poCretedOrders>0 && <div style={{width:(statusSummary.poCretedOrders/total)*100+"%"}} className={"poCretedOrders"}></div>}
                                {statusSummary.poAckOrders>0 && <div style={{width:(statusSummary.poAckOrders/total)*100+"%"}} className={"poAckOrders"}></div>}
                                {statusSummary.cancelled>0 && <div style={{width:(statusSummary.cancelled/total)*100+"%"}} className={"cancelled"}></div>}
                            </div>
                            <div className={"omsStatusDescriptionBox"}>
                                {statusSummary.draftOrders>0 && <div className={"omsStatusDescription"}>Draft - {statusSummary.draftOrders}</div>}
                                {statusSummary.createdOrders>0 && <div className={"omsStatusDescription"}>Created - {statusSummary.createdOrders}</div>}
                                {statusSummary.acocOrders>0 && <div className={"omsStatusDescription"}>ACOC - {statusSummary.acocOrders}</div>}
                                {statusSummary.poCretedOrders>0 && <div className={"omsStatusDescription"}>PO Created - {statusSummary.poCretedOrders}</div>}
                                {statusSummary.poAckOrders>0 && <div className={"omsStatusDescription"}>PO Ack - {statusSummary.poAckOrders}</div>}
                                {statusSummary.cancelled>0 && <div className={"omsStatusDescription"}>Cancelled - {statusSummary.cancelled}</div>}
                            </div>
                        </div>
                    )
                } else{
                   return (
                       <div></div>
                   )
                }

            }
        }
        return (
            <div>

            </div>
        )

    }

    render() {
        return (
            <div>
                {this.state.loading && <Loader/>}
                <div onClick={this.fetchData} className={"statusCount"}>{this.props.title}</div>
                {this.getOmsSummary()}
                <CSVLink
                    data={this.state.data}
                    filename={moment(this.props.cutoffDate).format("DDMMYYYYhhmm") + "_" + this.props.orderType.toLowerCase() + "_" + this.props.cutoffStatus.toLowerCase() + ".csv"}
                    className="hidden"
                    ref={this.csvLink}
                    target="_blank"
                />
            </div>
        )
    }
}
CSVDownloader.propTypes = {
    setLoader: PropTypes.func.isRequired,
}

const mapStateToProps = state  => ({

})

export default connect(mapStateToProps,{ setLoader })(withRouter(CSVDownloader))