import {GET_PO_CHECKLIST, TOGGLE_CHECKLIST_ITEM, DATA_LOADING} from '../actions/constants';

const initialState = {
    isAuthenticated: false,
    user: {}
};

export default function(state = initialState, action) {
    switch(action.type) {
        case GET_PO_CHECKLIST:
            let poCheckList = {
                "RFR": {
                    data:JSON.parse(action.payload.poCheckList.RFR),
                    collapse:false,
                    helpLink: "http://kibana-cpoar.prod.walmartlabs.com/goto/1b208cbf03d917d02ef0babe08cf16a5"
                },
                "FRS": {
                    data:JSON.parse(action.payload.poCheckList.FRS),
                    collapse:false,
                    helpLink:"http://kibana-cpoar.prod.walmartlabs.com/goto/1b208cbf03d917d02ef0babe08cf16a5"
                },
                "ACOC": {
                    data:JSON.parse(action.payload.poCheckList.ACOC),
                    collapse:false,
                    helpLink:"http://kibana-cpoar.prod.walmartlabs.com/goto/1b208cbf03d917d02ef0babe08cf16a5"
                },
                "RACOC": {
                    data:JSON.parse(action.payload.poCheckList.RACOC),
                    collapse:false,
                    helpLink:"http://kibana-cpoar.prod.walmartlabs.com/goto/1b208cbf03d917d02ef0babe08cf16a5"
                },
                "reservationResponseTime":{
                    data: action.payload.poCheckList.reservationResponseTime,
                    collapse:false,
                    helpLink:"https://medusa.walmart.com/d/000030748/create-reservation?orgId=1&refresh=1m&from=now-2h&to=now"
                },
                "maasQueueInfo":{
                    data: JSON.parse(action.payload.poCheckList.maasQueueInfo),
                    collapse:false,
                    helpLink:"http://maas-monitor-ui.prod.walmart.com:5601/goto/9a6a515f7cb227fbd7b47cd3897d8feb"
                },
            }
            return {
                ...state,
                loading:action.payload.loading,
                poCheckList: poCheckList
            };
        case DATA_LOADING:
            return {
                ...state,
                loading:action.payload.loading
            };
        case TOGGLE_CHECKLIST_ITEM:
            return {
                ...state,
                poCheckList: action.payload.poCheckList
            }
        default:
            return state;
    }
}