import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { withRouter } from 'react-router-dom';
import { collapsePlatform, updateDataCenter, updatePriority, updateActionStatus } from '../../actions/oneops-actions';

import { Collapse, CardBody, Card } from 'reactstrap';
import ToggleListItem from './ToggleListItem'
import { Icon } from 'react-icons-kit';
import {ic_refresh} from 'react-icons-kit/md/ic_refresh'

class ToggleList extends Component {
    constructor(props){
        super(props);
        this.state ={
            checked:false
        }
    }
    handleChange(checked) {
        this.setState({ checked:!this.state.checked });
    }
    render() {
        let { oneops  } = this.props
        return(
            <div className="row">
                {oneops.components.length>0 && <form className="form-inline" action="" style={{marginLeft:'1rem', marginBottom:'1rem'}}>
                    <div className="checkbox" style={{marginRight:'1rem'}}>
                        <label><input type="checkbox" checked={oneops.selectDfw} onChange={() => this.props.updateDataCenter('dfw', oneops)}/> &nbsp;&nbsp;DFW</label>
                    </div>
                    <div className="checkbox" style={{marginRight:'1rem'}}>
                        <label><input type="checkbox" checked={oneops.selectCdc} onChange={() => this.props.updateDataCenter('cdc', oneops)}/> &nbsp;&nbsp;CDC</label>
                    </div>
                    <div className="checkbox" style={{marginRight:'4rem'}}>
                        <label><input type="checkbox" checked={oneops.selectAzure} onChange={() => this.props.updateDataCenter('azure', oneops)}/> &nbsp;&nbsp;Azure</label>
                    </div>
                    <div className="checkbox" style={{marginRight:'1rem'}}>
                        <label><input type="checkbox" checked={oneops.selectPrimary} onChange={() => this.props.updatePriority('primary', oneops)}/> &nbsp;&nbsp;Primary</label>
                    </div>
                    <div className="checkbox" style={{marginRight:'1rem'}}>
                        <label><input type="checkbox" checked={oneops.selectSecondary} onChange={() => this.props.updatePriority('secondary', oneops)}/> &nbsp;&nbsp;Secondary</label>
                    </div>
                   {oneops.procedures && <div style={{position:"absolute", right:"2rem"}}>
                        <button type="button"  style={{border:"none", outline:'none', backgroundColor:"#e2aa21", "borderRadius":"3px"}} onClick={() => this.props.updateActionStatus(oneops)}><Icon style={{display: "inline-block", width:"20px", height:"20px", borderRadius:"10px", color: '#ffffff'}} icon={ic_refresh} size={20}/></button>
                    </div>}
                </form>}

                {
                    oneops.components.map((item, index) => (
                        <div key={index} className={"col-md-12"} style={{marginBottom:'1rem'}}>
                            <button onClick={() => this.props.collapsePlatform(item.platform, oneops.components)} style={{ marginBottom: '0.2rem', outline:'none', backgroundColor:'#f5f5f5', width:'100%', borderColor:'#cacaca', color:'#474747', padding:'1rem', textAlign:'left', fontWeight:'bold'}}>{item.platform + " - " + item.components.length + " component(s)"}</button>
                            <Collapse isOpen={item.collapse}>
                                <Card>
                                    <CardBody style={{color:'grey'}} >
                                        <ToggleListItem components={item.components} platform={item.platform} applications={item.applications} />
                                    </CardBody>
                                </Card>
                            </Collapse>
                        </div>
                    ))
                }
            </div>
        )
    }
}
ToggleList.propTypes = {
    collapsePlatform: PropTypes.func.isRequired,
    updateDataCenter: PropTypes.func.isRequired,
    updatePriority: PropTypes.func.isRequired,
    updateActionStatus: PropTypes.func.isRequired
}

const mapStateToProps = state  => ({
    oneops: state.oneops,
})

export default connect(mapStateToProps,{ collapsePlatform, updateDataCenter, updatePriority, updateActionStatus })(withRouter(ToggleList))