import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import { withRouter } from 'react-router-dom';
import Navbar from "../../components/Navbar/Navbar";
import Select from 'react-select';
import { Form, Input } from 'semantic-ui-react'
import ReactTags from 'react-tag-autocomplete'

import { MODULES } from '../../actions/constants';
import { getOarCommsEmails, addOarCommsEmail, sendCommsEmail } from '../../actions/order-delay-actions'
import { getUserInfo } from '../../actions/auth-actions'
import CustomLoader from "../App/CustomLoader";
import PropTypes from 'prop-types';
import './OarComms.css'
const verticalOptions = [
    { label: 'USGR', text: 'USGR', value: 'USGR' },
    { label: 'USGM', text: 'USGM', value: 'USGM' }
]
const mailTypeOptions = [
    { label: 'Escalation', text: 'Escalation', value: 'Escalation' },
    { label: 'De-escalation', text: 'De-escalation', value: 'De-escalation' }
]
class OarComms extends Component {

    constructor(props){
        super(props);
        this.state={
            availableRecipients:[],
            recipientName:"",
            recipientEmail:"",
            file:null
        }
    }

    componentDidMount() {
        this.props.getOarCommsEmails();
        this.props.getUserInfo()
    }

    isValidEmail(email) {
        let re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        return re.test(String(email).toLowerCase());
    }

    submit = () => {
        if(!this.state.recipientName || !this.state.recipientEmail){
            alert("Please enter name and email.")
        } else{
            if(!this.isValidEmail(this.state.recipientEmail)){
                alert("Email entered is not valid.")
            }
            else{
                this.props.addOarCommsEmail(this.state.recipientName, this.state.recipientEmail)
                this.setState({recipientName:"", recipientEmail:""})
            }
        }
    }

    getEmails(list){
        let results = [];
        for(let i=0; i<list.length; i++){
            results.push(list[i].value)
        }
        return results;
    }

    handleFormSubmit = (event) =>{
        event.preventDefault()

        let body = {
            "vertical":this.state.selectedVertical.value,
            "commType":this.state.selectedCommType.value,
            "recipients":this.getEmails(this.state.selectedEmails),
            "description":this.state.description,
            "currentStatus":this.state.currentStatus,
            "impact":this.state.impact,
            "rootCause":this.state.rootCause,
            "actionItems":this.state.actionItems,
            "teamsEnganged":this.state.teamsEnganged,
            "siteopsIncident":this.state.siteopsIncident,
            "nextUpdate":this.state.nextUpdate,
            "sentByUserFullName":this.props.auth.user.username,
            "sentByUserId":this.props.auth.user.userId,
            "sentByUserEmail":this.props.auth.user.email,
        }
        if(body.siteopsIncident=="" || body.siteopsIncident==null) body.siteopsIncident = "NA"
        if(body.nextUpdate=="" || body.nextUpdate==null) body.nextUpdate = "NA"
        if(!body.vertical || !body.commType || body.recipients.length<1 || !body.description || !body.currentStatus || !body.impact
        || !body.rootCause || !body.rootCause || !body.actionItems || !body.teamsEnganged){
            alert("Please enter all the mandatory fields.")
        }
        else{
            console.log(body)
            const formData = new FormData();
            let attachment = this.state.file;
            formData.append("model", JSON.stringify(body));
            formData.append("file", attachment);
            this.props.sendCommsEmail(formData);
            this.setState({
                "selectedVertical":[],
                "selectedCommType":[],
                "selectedEmails":[],
                "description":"",
                "currentStatus":"",
                "impact":"",
                "rootCause":"",
                "actionItems":"",
                "teamsEnganged":"",
                "siteopsIncident":"",
                "nextUpdate":"",
            })
        }
    }


        handleFileChange = (e) => {
            let inputFile = e.target.files[0];
            this.setState({
                file: inputFile,
            })
        }

    render() {
        let { auth, orderDelay } = this.props
        return (
            <div>
                {orderDelay.commsLoading && <CustomLoader/>}
                <Navbar/>
                <div className="jumbotron" style={{minHeight:'100vh'}}>
                    <h1>OAR Comms</h1>
                    <ul className="breadcrumb">
                        <li><Link to="home">Home</Link></li>
                        <li><Link to="cutoffMonitor">Cutoff Manager</Link></li>
                        <li>OAR Comms</li>
                    </ul>
                    <hr size="30"/>
                    <div className={"currentDateNote"}>* fields are optional</div>
                    {orderDelay.emails && <div className="row">
                        <div className={"col-md-6"}>
                            <Form onSubmit={this.handleFormSubmit}>
                                <div className={"row"} style={{marginBottom:'1rem'}}>
                                    <div className={"col-md-6"}>
                                        <div style={{fontWeight:'bold', paddingBottom:'0.2rem'}}>Select Vertical</div>
                                        <Select
                                            value={this.state.selectedVertical}
                                            onChange={module => {
                                                this.setState({selectedVertical:module})
                                            }}
                                            isMulti={false}
                                            options={verticalOptions}
                                            placeholder="Select vertical.."
                                            styles={{width:'100%'}}
                                            autoFocus={false}
                                            isSearchable={false}
                                        />
                                    </div>
                                    <div className={"col-md-6"}>
                                        <div style={{fontWeight:'bold', paddingBottom:'0.2rem'}}>Select Comm Type</div>
                                        <Select
                                            value={this.state.selectedCommType}
                                            onChange={module => {
                                                this.setState({selectedCommType:module})
                                            }}
                                            isMulti={false}
                                            options={mailTypeOptions}
                                            placeholder="Select comm type.."
                                            styles={{width:'100%'}}
                                            autoFocus={false}
                                            isSearchable={false}
                                        />
                                    </div>
                                </div>
                                <div className={"row"} style={{marginBottom:'1rem'}}>
                                    <div className={"col-md-12"}>
                                        <div style={{fontWeight:'bold', paddingBottom:'0.2rem'}}>Recipients</div>
                                        <Select
                                            value={this.state.selectedEmails}
                                            onChange={selectedEmails => {
                                                this.setState({selectedEmails:selectedEmails})
                                            }}
                                            isMulti={true}
                                            options={orderDelay.emails}
                                            placeholder="Select recipients.."
                                            styles={{width:'100%'}}
                                            autoFocus={false}
                                            isSearchable={true}
                                        />
                                    </div>
                                </div>
                                <Form.Group widths='equal'>
                                    <Form.Field inline>
                                        <Form.TextArea label='Description' placeholder='Issue description...' onChange={(value) => this.setState({description:value.target.value})} value={this.state.description}/>
                                    </Form.Field>
                                    <Form.Field inline>
                                        <Form.TextArea label='Current Status' placeholder='current status...' onChange={(value) => this.setState({currentStatus:value.target.value})} value={this.state.currentStatus}/>
                                    </Form.Field>
                                </Form.Group>
                                <Form.Group widths='equal'>
                                    <Form.Field inline>
                                        <Form.TextArea label='Impact' placeholder='issue impact...' onChange={(value) => this.setState({impact:value.target.value})} value={this.state.impact}/>
                                    </Form.Field>
                                    <Form.Field inline>
                                        <Form.TextArea label='Root Cause' placeholder='root cause...' onChange={(value) => this.setState({rootCause:value.target.value})} value={this.state.rootCause}/>
                                    </Form.Field>
                                </Form.Group>
                                <Form.Group widths='equal'>
                                    <Form.Field inline>
                                        <Form.TextArea label='Action Items' placeholder='action items...' onChange={(value) => this.setState({actionItems:value.target.value})} value={this.state.actionItems}/>
                                    </Form.Field>
                                    <Form.Field inline>
                                        <Form.TextArea label='Teams Engaged' placeholder='teams engaged...'  onChange={(value) => this.setState({teamsEnganged:value.target.value})} value={this.state.teamsEnganged}/>
                                    </Form.Field>
                                </Form.Group>
                                <Form.Group widths='equal'>
                                    <Form.Field inline>
                                        <Form.Input fluid label='*Sitepos Incident' placeholder='siteops incident id...' onChange={(value) => this.setState({siteopsIncident:value.target.value})} value={this.state.siteopsIncident}/>
                                    </Form.Field>
                                    <Form.Field inline>
                                        <Form.Input fluid label='*Next Update' placeholder='next update...' onChange={(value) => this.setState({nextUpdate:value.target.value})} value={this.state.nextUpdate}/>
                                    </Form.Field>
                                </Form.Group>
                                <Form.Group widths='equal'>
                                    <Form.Field inline>
                                        <Form.Input fluid label='*Attachment' placeholder='optional attachment...' type={"file"} onChange={this.handleFileChange}/>
                                    </Form.Field>
                                </Form.Group>
                                <div className="col-md-3" style={{marginTop:"2rem", marginLeft:"-1rem"}}>
                                    <button type="submit" className="btn btn-default" style={{"backgroundColor":"#35b96f", "color":"white", "width":"100%", outline:'none'}}>Submit</button>
                                </div>
                            </Form>

                        </div>
                        <div className={"col-md-3"}>

                        </div>
                        <div className={"col-md-3"}>
                            <div className={"recipientConfigBox"}>
                                <h3>Configure Recipients</h3>
                                <Form>
                                    <Form.Group widths='equal'>
                                        <Form.Field inline>
                                            <Form.Input fluid label='Recipient Name' placeholder='recipient name...' onChange={(text) => this.setState({recipientName:text.target.value})} value={this.state.recipientName}/>
                                        </Form.Field>
                                    </Form.Group>
                                    <Form.Group widths='equal'>
                                        <Form.Field inline>
                                            <Form.Input fluid label='Email' placeholder='email...' onChange={(text) => this.setState({recipientEmail:text.target.value})} value={this.state.recipientEmail}/>
                                        </Form.Field>
                                    </Form.Group>
                                </Form>
                                <div className="col-md-6" style={{marginTop:"2rem", marginLeft:"-1rem"}}>
                                    <button type="button" className="btn btn-default" style={{"backgroundColor":"#35b96f", "color":"white", "width":"100%", outline:'none'}} onClick={this.submit}>Add Recipient</button>
                                </div>
                            </div>
                        </div>

                    </div>}

                </div>
            </div>
        );
    }
}

OarComms.propTypes = {
    auth:PropTypes.object,
    orderDelay:PropTypes.object,
    addOarCommsEmail:PropTypes.func.isRequired,
    getOarCommsEmails:PropTypes.func.isRequired,
    getUserInfo:PropTypes.func.isRequired,
    sendCommsEmail:PropTypes.func.isRequired
}

const mapStateToProps = state  => ({
    auth:state.auth,
    orderDelay:state.orderDelay
})

export default connect( mapStateToProps, {addOarCommsEmail, getOarCommsEmails,getUserInfo, sendCommsEmail })(withRouter(OarComms))

