import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import Moment from 'react-moment';
import { withRouter } from 'react-router-dom';
import { Pagination, Table } from 'semantic-ui-react'
import Select from 'react-select';

import Navbar from "../../components/Navbar/Navbar";
import { getReports, subscribeReport, unsubscribeReport} from '../../actions/oar-reports-actions'
import { getUserInfo } from '../../actions/auth-actions'
import CustomLoader from "../App/CustomLoader";
import { Icon } from 'react-icons-kit'
import {github} from 'react-icons-kit/icomoon/github'
import {ic_help} from 'react-icons-kit/md/ic_help'
import {ic_settings_applications} from 'react-icons-kit/md/ic_settings_applications'
import {eye} from 'react-icons-kit/icomoon/eye'
import './ReportsManager.css'
class ReportsManager extends Component {

    constructor(props){
        super(props);
        this.state={
            reportName:"",
            pageNumber:0,
            pageSize:{
                label:10,
                value:10
            },
            reportsToShow:[]
        }
    }

    componentDidMount() {
        this.props.getReports();
        this.props.getUserInfo()
    }

    updateSystem = selected => {
        if(selected.label==="ALL"){
            this.setState({selectedSystem: null, pageNumber:0})
        }
        else this.setState({selectedSystem: selected, pageNumber:0})
    };

    updateUser = selected => {
        if(selected.label==="ALL"){
            this.setState({selectedUser: null, pageNumber:0})
        }
        else this.setState({selectedUser: selected, pageNumber:0})
    };

    getPaginatedView(reports){
        let reportsToShow = reports
                .filter(item => item.reportName.toLowerCase().includes(this.state.reportName.toLowerCase()))
                .filter(item => this.state.selectedSystem?item.system.toUpperCase().includes(this.state.selectedSystem.label):item)
                .filter(item => this.state.selectedUser?item.userId.includes(this.state.selectedUser.label):item)
        return reportsToShow.slice(this.state.pageNumber*this.state.pageSize.value, (this.state.pageNumber+1)*this.state.pageSize.value);
    }

    getReportsToShow(reports){
        let reportsToShow = reports.filter(item => item.reportName.toLowerCase().includes(this.state.reportName.toLowerCase()))
            .filter(item => this.state.selectedSystem?item.system.toUpperCase().includes(this.state.selectedSystem.label):item)
            .filter(item => this.state.selectedUser?item.userId.includes(this.state.selectedUser.label):item)
        return reportsToShow;
    }

    handlePaginationChange = (e, { activePage })  => {
        this.setState({pageNumber:activePage-1})
    }

    render() {
        let { reports  } = this.props
        return (
            <div>
                {reports.loading && <CustomLoader/>}
                <Navbar/>
                <div className="jumbotron" style={{minHeight:'100vh'}}>
                    <h1>OAR Reports Manager</h1>
                    <ul className="breadcrumb">
                        <li><Link to="home">Home</Link></li>
                        <li>OAR Reports Manager</li>
                    </ul>
                    <hr size="30"/>
                    <form action="" className={"reportsManagerForm"}>
                        <div className={"row"}>
                            <div className={"col-md-3"}>
                                <div className="form-group">
                                    <label htmlFor="email">Search by report name</label>
                                    <input type="text" className="form-control" id="reportName" onChange={(reportName) => this.setState({reportName:reportName.target.value})}/>
                                </div>
                            </div>
                            <div className={"col-md-2"}>
                                <button type="submit" className="btn btn-info" style={{marginTop:'2rem'}}>Search</button>
                            </div>
                            <div className={"col-md-2"}>
                            </div>
                            <div className={"col-md-1"}>
                                <div className="form-group">
                                    <label htmlFor="email">Page Size</label>
                                    <Select
                                        value={this.state.pageSize}
                                        onChange={(option) => this.setState({pageSize:option, pageNumber:0})}
                                        options={[{
                                            label:10,
                                            value:10
                                        },{
                                            label:25,
                                            value:25
                                        },{
                                            label:50,
                                            value:50
                                        }]}
                                    />
                                </div>
                            </div>
                            <div className={"col-md-2"}>
                                <div className="form-group">
                                    <label htmlFor="email">Filter by system</label>
                                    <Select
                                        value={this.state.selectedSystem}
                                        onChange={this.updateSystem}
                                        options={reports.allSystems}
                                        placeholder="Select system.."
                                    />
                                </div>
                            </div>
                            <div className={"col-md-2"}>
                                <div className="form-group">
                                    <label htmlFor="email">Filter by user</label>
                                    <Select
                                        value={this.state.selectedUser}
                                        onChange={this.updateUser}
                                        options={reports.allUsers}
                                        placeholder="Select userId.."
                                    />
                                </div>
                            </div>
                        </div>
                    </form>
                    <div style={{display:'flex', flexDirection:'row-reverse'}}>
                        {reports.reports && <div className={"paginationBox"}>
                            <div className={"reportsCount"}>
                                ({this.getReportsToShow(reports.reports).length} reports)
                            </div>
                            <div className={"paginationText"}>
                                <Pagination
                                    defaultActivePage={1}
                                    firstItem={null}
                                    lastItem={null}
                                    siblingRange={null}
                                    onPageChange={this.handlePaginationChange}
                                    totalPages={Math.ceil(this.getReportsToShow(reports.reports).length/this.state.pageSize.value)}
                                />
                            </div>
                        </div>}
                    </div>
                    <Table celled striped>
                        <Table.Header>
                            <Table.Row>
                                <Table.HeaderCell>Report Name</Table.HeaderCell>
                                <Table.HeaderCell>Description</Table.HeaderCell>
                                <Table.HeaderCell>Links</Table.HeaderCell>
                                <Table.HeaderCell>System</Table.HeaderCell>
                                <Table.HeaderCell>Frequency</Table.HeaderCell>
                                <Table.HeaderCell>Created On</Table.HeaderCell>
                                <Table.HeaderCell>Created By</Table.HeaderCell>
                                <Table.HeaderCell>Subscribe/Unsubscribe</Table.HeaderCell>
                                <Table.HeaderCell></Table.HeaderCell>
                            </Table.Row>
                        </Table.Header>
                        <Table.Body>

                            {
                                reports.reports && this.getPaginatedView(reports.reports)
                                    .map((item, index) => (

                                        <Table.Row key={index}>
                                            <Table.Cell>{item.reportName}</Table.Cell>
                                            <Table.Cell>{item.description}</Table.Cell>
                                            <Table.Cell>
                                                <div style={{display:'inlineBlock', minWidth:'6rem'}}>
                                                    {item.gitUrl &&  <a href={item.gitUrl} target={"blank"} style={{display:'inlineBlock', marginRight:'0.3rem', color:'black'}}><Icon icon={github} size={17}/></a>}
                                                    {item.baasUrl && <a href={item.baasUrl} target={"blank"} style={{display:'inlineBlock', marginRight:'0.3rem', color:'black'}}><Icon icon={ic_settings_applications} size={21}/></a>}
                                                    {item.helpUrl && <a href={item.helpUrl} target={"blank"} style={{display:'inlineBlock', marginRight:'0.3rem', color:'black'}}><Icon icon={ic_help} size={19}/></a>}
                                                </div>
                                            </Table.Cell>
                                            <Table.Cell>{item.system.toUpperCase()}</Table.Cell>
                                            <Table.Cell>{(item.frequency/60000)>60?(( (item.frequency/3600000)>24? ((item.frequency/86400000) + " day(s)"):((item.frequency/3600000) + " hour(s)") )): ((item.frequency/60000) + " min(s)")}</Table.Cell>
                                            <Table.Cell>
                                                <Moment format="D MMM YYYY" withTitle>
                                                    {item.createdOn}
                                                </Moment>
                                            </Table.Cell>
                                            <Table.Cell>{item.userId}</Table.Cell>
                                            {!item.isSubscribed && <Table.Cell><button type="button" className="btn btn-success" style={{fontSize:'1rem', height:'2rem', marginTop:'-0.1rem' ,paddingTop:'3px', outline:'none !important'}} onClick={()=>{
                                                this.props.subscribeReport(item.reportName)
                                                this.props.getReports();
                                            }}>Subscribe</button></Table.Cell>}
                                            {item.isSubscribed && <Table.Cell><button type="button" className="btn btn-info" style={{fontSize:'1rem', height:'2rem', marginTop:'-0.1rem' ,paddingTop:'3px', color:'white', outline:'none !important'}} onClick={()=>{
                                                this.props.unsubscribeReport(item.reportName)
                                                this.props.getReports();
                                            }}>Unsubscribe</button></Table.Cell>}
                                            <Table.Cell collapsing>
                                                <Link to={"report/" + item.reportName} style={{color:'black'}}><Icon icon={eye} size={18}/></Link>
                                            </Table.Cell>
                                        </Table.Row>
                                    ))
                            }
                        </Table.Body>
                    </Table>

                </div>
            </div>
        );
    }
}

ReportsManager.propTypes = {
    reports:PropTypes.object,
    getUserInfo:PropTypes.func.isRequired,
    getReports: PropTypes.func.isRequired,
    subscribeReport: PropTypes.func.isRequired,
    unsubscribeReport: PropTypes.func.isRequired
}

const mapStateToProps = state  => ({
    reports:state.reports
})

export default connect(mapStateToProps, { getReports, getUserInfo, subscribeReport, unsubscribeReport})(withRouter(ReportsManager))

