import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import { withRouter } from 'react-router-dom';
import Navbar from "../../components/Navbar/Navbar";
import Select from 'react-select';
import { MODULES } from '../../actions/constants';
import { addUserAccess } from '../../actions/auth-actions'
import { Input } from 'semantic-ui-react'
import CustomLoader from "../App/CustomLoader";
import PropTypes from 'prop-types';

class ManageAccess extends Component {

    constructor(props){
        super(props);
        let availableModules = [];
        for(let i=0; i<MODULES.length; i++){
            let option = {
                "label": MODULES[i],
                "value": MODULES[i]
            }
            availableModules.push(option);
        }
        this.state={
            availableModules:availableModules,
            selectedModules:[]
        }
    }

    componentDidMount() {

    }

    updateModules = selected => {
        this.setState({selectedModules: selected})
    };

    submit = () => {
        if(!this.state.userId){
            alert("Please enter a valid User Id.")
        } else if(this.state.userId.length<3 || this.state.userId.length>12){
            alert("Please enter a valid User Id.")
        } else if(!this.state.selectedModules){
          alert("Please select at least one module.")
        } else if(this.state.selectedModules.length<1){
            alert("Please select at least one module.")
        } else{
            let modules = [];
            for(let i=0; i<this.state.selectedModules.length; i++){
                modules.push(this.state.selectedModules[i].value)
            }
            let body = {
                modules: modules,
                userId: this.state.userId
            }
            this.props.addUserAccess(body)
            this.setState({selectedModules:[], userId:""})
        }
    }

    render() {
        let { auth } = this.props
        return (
            <div>
                {auth.accessRequestLoading && <CustomLoader/>}
                <Navbar/>
                <div className="jumbotron" style={{minHeight:'100vh'}}>
                    <h1>Manage User Access</h1>
                    <ul className="breadcrumb">
                        <li><Link to="home">Home</Link></li>
                        <li>Manage User Access</li>
                    </ul>
                    <hr size="30"/>
                    <div className="row">
                        <div className="col-md-5">
                            <label>Select Modules</label>
                            <Select
                                value={this.state.selectedModules}
                                onChange={modules => {
                                    this.updateModules(modules)
                                }}
                                isMulti={true}
                                options={this.state.availableModules}
                                placeholder="Select modules.."
                            />
                        </div>
                        <div className="col-md-3">
                            <label>Enter User Id</label>
                            <Input placeholder='User Id..' value={this.state.userId} onChange={(event, data) => this.setState({userId:data.value})} style={{width:'100%'}}/>
                        </div>
                        <div className="col-md-2" style={{marginTop:"2rem"}}>
                            <button type="button" className="btn btn-default" style={{"backgroundColor":"#35b96f", "color":"white", "width":"100%", outline:'none'}} onClick={this.submit}>Submit</button>
                        </div>
                    </div>

                </div>
            </div>
        );
    }
}

ManageAccess.propTypes = {
    auth:PropTypes.object,
    addUserAccess: PropTypes.func.isRequired
}

const mapStateToProps = state  => ({
    auth:state.auth
})

export default connect(mapStateToProps, { addUserAccess })(withRouter(ManageAccess))

