import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import { withRouter } from 'react-router-dom';
import Navbar from "../../../components/Navbar/Navbar";
import { Form } from 'semantic-ui-react';
import ReactTags from 'react-tag-autocomplete'
import ReactQuill from 'react-quill';
import { getReports, subscribeReport, unsubscribeReport, getReportsDetails, updateReportDetails, updateReport, getIssuesTrend} from '../../../actions/oar-reports-actions'
import { getUserInfo } from '../../../actions/auth-actions';

import { Icon } from 'react-icons-kit'
import { arrowUpRight } from 'react-icons-kit/icomoon/arrowUpRight'
import CustomLoader from '../../App/CustomLoader'
import './ViewReport.css';
const ReactHighstock = require('react-highcharts/ReactHighstock');

class ViewReport extends Component {

    constructor(props){
        super(props);
        this.state={
            text: '',
            stats:{
                rangeSelector: {
                    selected: 1
                },
                title: {
                    text: 'Order Download'
                },
                series: [{
                    name: 'Order Download',
                    data: [],
                    tooltip: {
                        valueDecimals: 2
                    }
                }]
            }
        }
    }
    componentDidMount() {
        this.props.getUserInfo()
        this.props.getReports();
        this.props.getReportsDetails(this.props.match.params.reportName)
        this.props.getIssuesTrend(this.props.match.params.reportName)
    }


    handleSubscriberDelete (i, reportDetails) {
        const subscribers = reportDetails.subscribers.slice(0)
        subscribers.splice(i, 1)
        this.props.updateReportDetails(reportDetails, "SUBSCRIBERS", subscribers)
    }

    handleSubscriberAddition (tag, reportDetails) {
        console.log(tag.name)
        const tags = [].concat(reportDetails.subscribers, tag.name.toLowerCase())
        this.props.updateReportDetails(reportDetails, "SUBSCRIBERS", tags)
    }

    getSubscribersTags(emails){
        let subTags = [];
        for(let i=0; i<emails.length; i++){
            subTags.push({
                id:emails[i],
                name:emails[i]
            })
        }
        return subTags;
    }

    getAllSystems(systems){
        let sys = [];
        for(let i=0; i<systems.length; i++){
            sys.push({
                key:systems[i].value,
                text:systems[i].label,
                value:systems[i].value
            })
        }
        return sys;
    }

    handleSystemChange = (e, data, reportDetails) => {
        this.props.updateReportDetails(reportDetails, "SYSTEM", data.value)
    }

    render() {
        let { reports, auth  } = this.props
        let hasWriteAccess = (auth.userAccess.includes("ADMIN") || auth.userAccess.includes("REPORTS_MANAGER_RW"))?true:false
        //let hasWriteAccess = false
        console.log(hasWriteAccess)
        return (
            <div>
                {reports.loading && <CustomLoader/>}
                <Navbar/>
                <div className="jumbotron" style={{minHeight:'100vh'}}>
                    <h1>OAR Reports Manager</h1>
                    <ul className="breadcrumb">
                        <li><Link to="/home">Home</Link></li>
                        <li><Link to="/reports">All Reports</Link></li>
                        <li>{this.props.match.params.reportName}</li>
                    </ul>
                    <hr size="30"/>
                    { reports.reportDetails && reports.allSystems &&
                    <div className={"row"}>
                        <div className={"col-md-8"}>
                            <Form>
                                <Form.Group widths='equal'>
                                    <div className={"inputBox"}>
                                        <div style={{fontWeight:'bold', paddingBottom:'0.2rem', display:"inline-block"}}>Report Id</div>
                                        <Form.Input fluid value={reports.reportDetails.reportName} disabled={!hasWriteAccess}/>
                                    </div>
                                    <div className={"inputBox"}>
                                        <div style={{fontWeight:'bold', paddingBottom:'0.2rem', display:"inline-block"}}>System</div>
                                    <Form.Select
                                        fluid
                                        options={this.getAllSystems(reports.allSystems)}
                                        value={reports.reportDetails.system}
                                        placeholder='Select system..'
                                        disabled={!hasWriteAccess}
                                        onChange={(e,data) => this.handleSystemChange(e,data,reports.reportDetails)}
                                    />
                                    </div>
                                    <div className={"inputBox"}>
                                        <div style={{fontWeight:'bold', paddingBottom:'0.2rem', display:"inline-block"}}>Git URL <a href={reports.reportDetails.gitUrl} target={"blank"} style={{display:'inlineBlock', marginLeft:'0.2rem', color:'black'}}><Icon icon={arrowUpRight} size={12}/></a></div>
                                        <Form.Input fluid value={reports.reportDetails.gitUrl} disabled={!hasWriteAccess}
                                                    onChange={(value) => this.props.updateReportDetails(reports.reportDetails, "GIT_URL", value)}/>
                                    </div>
                                    <div className={"inputBox"}>
                                        <div style={{fontWeight:'bold', paddingBottom:'0.2rem', display:"inline-block"}}>BAAS URL <a href={reports.reportDetails.baasUrl} target={"blank"} style={{display:'inlineBlock', marginLeft:'0.2rem', color:'black'}}><Icon icon={arrowUpRight} size={12}/></a></div>
                                        <Form.Input fluid value={reports.reportDetails.baasUrl} disabled={!hasWriteAccess}
                                                    onChange={(value) => this.props.updateReportDetails(reports.reportDetails, "BAAS_URL", value)}/>
                                    </div>
                                </Form.Group>
                                <Form.Group widths='equal'>
                                    <div className={"inputBox"}>
                                        <div style={{fontWeight:'bold', paddingBottom:'0.2rem', display:"inline-block"}}>Help URL <a href={reports.reportDetails.helpUrl} target={"blank"} style={{display:'inlineBlock', marginLeft:'0.2rem', color:'black'}}><Icon icon={arrowUpRight} size={12}/></a></div>
                                        <Form.Input fluid value={reports.reportDetails.helpUrl?reports.reportDetails.helpUrl:""} disabled={!hasWriteAccess}
                                                onChange={(value) => this.props.updateReportDetails(reports.reportDetails, "HELP_URL", value)}/>
                                    </div>
                                    <div className={"inputBox"}>
                                        <div style={{fontWeight:'bold', paddingBottom:'0.2rem', display:"inline-block"}}>Frequency</div>
                                        <Form.Input fluid value={reports.reportDetails.frequency} disabled={!hasWriteAccess}
                                                onChange={(value) => this.props.updateReportDetails(reports.reportDetails, "FREQUENCY", value)}/>
                                    </div>
                                    <div className={"inputBox"}>
                                        <div style={{fontWeight:'bold', paddingBottom:'0.2rem', display:"inline-block"}}>Threshold</div>
                                        <Form.Input fluid value={reports.reportDetails.threshold?reports.reportDetails.threshold:""} disabled={!hasWriteAccess}
                                                onChange={(value) => this.props.updateReportDetails(reports.reportDetails, "THRESHOLD", value)}/>
                                    </div>
                                    <div className={"inputBox"}>
                                        <div style={{fontWeight:'bold', paddingBottom:'0.2rem', display:"inline-block"}}>Last execution time</div>
                                        <Form.Input fluid value={reports.reportDetails.lastRun?reports.reportDetails.lastRun:""} disabled={!hasWriteAccess}/>
                                    </div>
                                    </Form.Group>
                                <div style={{fontWeight:'bold', paddingBottom:'0.2rem'}}>Subscribers</div>
                                <ReactTags
                                    tags={this.getSubscribersTags(reports.reportDetails.subscribers)}
                                    suggestions={[]}
                                    placeholderText="Add new subscriber.."
                                    onDelete={i => this.handleSubscriberDelete(i, reports.reportDetails )}
                                    onAddition={tag => this.handleSubscriberAddition(tag, reports.reportDetails)}
                                    allowNew={hasWriteAccess}
                                />
                                <Form.TextArea label='Description' placeholder='Report description...' value={reports.reportDetails.description} disabled={!hasWriteAccess}
                                               onChange={(value) => this.props.updateReportDetails(reports.reportDetails, "DESCRIPTION", value)}/>
                                <div style={{fontWeight:'bold', paddingBottom:'0.2rem'}}>Validation steps</div>
                                <ReactQuill value={reports.reportDetails.debuggingSteps}
                                            onChange={(value) => this.props.updateReportDetails(reports.reportDetails, "DEBUGGING_STEPS", value)}
                                            style={{
                                                backgroundColor:'white',
                                                marginBottom:'1.5rem'
                                            }}
                                            placeholder={"Debugging steps not available."}
                                            readOnly={!hasWriteAccess}
                                />
                                {hasWriteAccess && <Form.Button onClick={() =>this.props.updateReport(reports.reportDetails)} style={{backgroundColor:'#226192', color:'white'}}>Save</Form.Button>}
                            </Form>
                        </div>
                        <div className={"col-md-4"}>
                            <div className={"row"}>
                                <div className={"col-md-6"}>
                                    <div className={"statBox"}>
                                        <div className={"statCount"}>{parseInt(reports.reportDetails.issuesCaptured)}</div>
                                        <div className={"statText"}>Issues Captured</div>
                                    </div>
                                </div>
                                <div className={"col-md-6"}>
                                    <div className={"statBox"}>
                                        <div className={"statCount"}>{parseInt(reports.reportDetails.issuesFixed)}</div>
                                        <div className={"statText"}>Issues Fixed</div>
                                    </div>
                                </div>
                            </div>
                            <div className={"totalIterations"}>*{reports.reportDetails.totalIterations} total iterations</div>
                            <div className={"statsGraph"}>
                               {reports.issuesTrend &&  <ReactHighstock config={reports.issuesTrend} ref="chart"></ReactHighstock>}
                            </div>
                        </div>
                    </div>
                    }
                </div>
            </div>
        );
    }
}

ViewReport.propTypes = {
    reports:PropTypes.object,
    auth:PropTypes.object,
    getReports: PropTypes.func.isRequired,
    getUserInfo:PropTypes.func.isRequired,
    getReportsDetails: PropTypes.func.isRequired,
    subscribeReport: PropTypes.func.isRequired,
    unsubscribeReport: PropTypes.func.isRequired,
    updateReportDetails: PropTypes.func.isRequired,
    updateReport:PropTypes.func.isRequired,
    getIssuesTrend:PropTypes.func.isRequired
}

const mapStateToProps = state  => ({
    reports:state.reports,
    auth:state.auth
})

export default connect(mapStateToProps, {getReports, getUserInfo, updateReportDetails, updateReport, subscribeReport, unsubscribeReport, getReportsDetails, getIssuesTrend})(withRouter(ViewReport))

