import axios from 'axios';
import {
    BASE_URL,
    DATA_LOADING,
    GET_ERRORS,
    GET_REPORTS,
    GET_REPORT_DETAILS,
    GET_ISSUES_TREND
} from './constants'

Array.prototype.contains = function(v) {
    for (let i = 0; i < this.length; i++) {
        if (this[i] === v) return true;
    }
    return false;
};
Array.prototype.unique = function() {
    let arr = [];
    for (let i = 0; i < this.length; i++) {
        if (!arr.contains(this[i])) {
            arr.push(this[i]);
        }
    }
    return arr;
}

export const getReports = () => dispatch => {
    dispatch({
        type: DATA_LOADING,
        payload: {
            loading:true
        }
    });
    axios.get(BASE_URL + "getOarReports")
        .then(response => {
            let allReports = response.data;
            let body = {
                email: localStorage.getItem('nirvanaEmail').toLowerCase()
            }
            Array.prototype.contains = function(v) {
                for (let i = 0; i < this.length; i++) {
                    if (this[i] === v) return true;
                }
                return false;
            };
            Array.prototype.unique = function() {
                var arr = [];
                for (var i = 0; i < this.length; i++) {
                    if (!arr.contains(this[i])) {
                        arr.push(this[i]);
                    }
                }
                return arr;
            }

            let userIds = [];
            let systems = [];
            for(let i=0; i<allReports.length; i++){
                userIds.push(allReports[i].userId);
                systems.push(allReports[i].system);
            }
            axios.post(BASE_URL + "getSubscriptions", body)
                .then(response => {
                    let userReports = response.data;

                    for(let i=0; i<allReports.length; i++){
                        if(userReports.includes(allReports[i].reportName)){
                            allReports[i].isSubscribed = true;
                        }
                        else{
                            allReports[i].isSubscribed = false;
                        }
                    }
                    let allSystems = [{
                        label:"ALL",
                        value:"ALL"
                    }];
                    let allUsers = [{
                        label:"ALL",
                        value:"ALL"
                    }];
                    for(let i=0; i<systems.unique().length; i++){
                        let temp = {
                            label: systems.unique()[i].toUpperCase(),
                            value: systems.unique()[i],
                        }
                        allSystems.push(temp);
                    }
                    for(let i=0; i<userIds.unique().length; i++){
                        let temp = {
                            label: userIds.unique()[i],
                            value: userIds.unique()[i],
                        }
                        allUsers.push(temp);
                    }
                    dispatch({
                        type: GET_REPORTS,
                        payload: {
                            loading:false,
                            reports:allReports,
                            userReports:userReports,
                            allSystems:allSystems,
                            allUsers:allUsers
                        }
                    });
                })
                .catch(error => {
                    dispatch({
                        type: GET_ERRORS,
                        payload: error
                    });
                })


        })
        .catch(error => {
            dispatch({
                type: GET_ERRORS,
                payload: error
            });
        })
}

