export const getReportsDetails = (reportName) => dispatch => {
    dispatch({
        type: GET_REPORT_DETAILS,
        payload: {
            loading:true,
            reportDetails:null
        }
    });
    axios.get(BASE_URL + "getReportDetails/" + reportName)
        .then(response => {
            dispatch({
                type: GET_REPORT_DETAILS,
                payload: {
                    reportDetails:response.data,
                    loading:false
                }
            });
        })
        .catch(error => {
            dispatch({
                type: GET_ERRORS,
                payload: error
            });
        })
}

export const updateReport = (reportDetails) => dispatch => {
    dispatch({
        type: DATA_LOADING,
        payload: {
            loading:true
        }
    });
    console.log(reportDetails)
    let report = {
        reportName: reportDetails.reportName,
        description:reportDetails.description?reportDetails.description:"",
        system:reportDetails.system,
        baasUrl:reportDetails.baasUrl?reportDetails.baasUrl:"",
        gitUrl:reportDetails.gitUrl?reportDetails.gitUrl:"",
        frequency:reportDetails.frequency?reportDetails.frequency:"",
        helpUrl:reportDetails.helpUrl?reportDetails.helpUrl:"",
        threshold:reportDetails.threshold?reportDetails.threshold:"",
        debuggingSteps:reportDetails.debuggingSteps?reportDetails.debuggingSteps:"",
        lastRun:reportDetails.lastRun?reportDetails.lastRun:"",
        subscribers:reportDetails.subscribers
    }
    axios.post(BASE_URL + "updateReportDetails",report)
        .then(res => {
            if(res.data.message==="OK"){
                axios.get(BASE_URL + "getReportDetails/" + reportDetails.reportName)
                    .then(response => {
                        dispatch({
                            type: GET_REPORT_DETAILS,
                            payload: {
                                reportDetails:response.data,
                                loading:false
                            }
                        });
                    })
                    .catch(error => {
                        dispatch({
                            type: GET_ERRORS,
                            payload: {
                                error:error,
                                loading:false
                            }
                        });
                    })
            } else{
                dispatch({
                    type: GET_ERRORS,
                    payload: {
                        error:"Error while updating report.",
                        loading:false
                    }
                });
            }

        })
        .catch(error => {
            dispatch({
                type: GET_ERRORS,
                payload: error
            });
        })
}

export const updateReportDetails = (reportDetails, fieldName, fieldValue) => dispatch => {
    if(fieldName==="HELP_URL"){
        reportDetails.helpUrl = fieldValue.target.value;
    } else if(fieldName==="FREQUENCY"){
        reportDetails.frequency = fieldValue.target.value;
    } else if(fieldName==="THRESHOLD"){
        reportDetails.threshold = fieldValue.target.value;
    } else if(fieldName==="DESCRIPTION"){
        reportDetails.description = fieldValue.target.value;
    } else if(fieldName==="GIT_URL"){
        reportDetails.gitUrl = fieldValue.target.value;
    } else if(fieldName==="BAAS_URL"){
        reportDetails.baasUrl = fieldValue.target.value;
    } else if(fieldName==="SYSTEM"){
        reportDetails.system = fieldValue;
    } else if(fieldName==="SUBSCRIBERS"){
        reportDetails.subscribers = fieldValue;
    } else if(fieldName==="DEBUGGING_STEPS"){
        reportDetails.debuggingSteps = fieldValue;
    }
    dispatch({
        type: GET_REPORT_DETAILS,
        payload: {
            reportDetails:reportDetails,
            loading:false
        }
    });
}

export const getIssuesTrend = (reportName) => dispatch => {
    dispatch({
        type: DATA_LOADING,
        payload: {
            loading:true
        }
    });
    let body = {
        "size": 0,
        "query": {
            "bool": {
                "must": [
                    {
                        "match": {
                            "reportName": reportName
                        }
                    },
                    {
                        "range" : {
                            "createTs" : {
                                "gte" : "now-30d",
                                "lte" : "now"
                            }
                        }
                    }
                ]
            }
        },
        "aggs": {
            "issuesCapturedOverTime": {
                "date_histogram": {
                    "field": "createTs",
                    "interval": "1d",
                    "format": "yyyy-MM-dd"
                },
                "aggs": {
                    "issuesCaptured": {
                        "sum": {
                            "field": "issuesCaptured"
                        }
                    }
                }
            },
            "issuesFixedOverTime": {
                "date_histogram": {
                    "field": "createTs",
                    "interval": "1d",
                    "format": "dd-MM-yyyy"
                },
                "aggs": {
                    "issuesFixed": {
                        "sum": {
                            "field": "issuesFixed"
                        }
                    }
                }
            }
        }
    }
    axios.post("http://es-cpoar.prod.walmart.com:9200/oar_report_logs/_search", body)
        .then(response => {
            let issuesFixedOverTimeRaw = response.data.aggregations.issuesFixedOverTime.buckets;
            let issuesCapturedOverTimeRaw = response.data.aggregations.issuesCapturedOverTime.buckets;
            let issuesFixedOverTime = [];
            let issuesCapturedOverTime = [];
            for(let i=0; i<issuesFixedOverTimeRaw.length; i++){
                let temp = [issuesFixedOverTimeRaw[i].key, issuesFixedOverTimeRaw[i].issuesFixed.value]
                issuesFixedOverTime.push(temp);
            }
            for(let i=0; i<issuesCapturedOverTimeRaw.length; i++){
                let temp = [issuesCapturedOverTimeRaw[i].key, issuesCapturedOverTimeRaw[i].issuesCaptured.value]
                issuesCapturedOverTime.push(temp);
            }
            dispatch({
                type: GET_ISSUES_TREND,
                payload: {
                    issuesTrend: {
                        rangeSelector: {
                            selected: 1
                        },
                        title: {
                            text: 'Issues Trend (30 Days)'
                        },
                        series: [{
                            name: 'Issues Captured',
                            data: issuesCapturedOverTime,
                            tooltip: {
                                valueDecimals: 0
                            }
                        },{
                            name: 'Issues Fixed',
                            data: issuesCapturedOverTime,
                            tooltip: {
                                valueDecimals: 0
                            }
                        }]
                    },
                    loading:false
                }
            });
        })
        .catch(error => {
            dispatch({
                type: GET_ERRORS,
                payload: {
                    error:error,
                    loading:false
                }
            });
        })

}