import axios from 'axios';
import {
    BASE_URL,
    ONEOPS_DATA_LOADING,
    ONEOPS_GET_ERRORS,
    ONEOPS_HEALTH
} from './constants'

export const getHealth = () => dispatch => {
    dispatch({
        type: ONEOPS_DATA_LOADING,
        payload: {
            oneopsHealthLoading: true
        }
    });
    axios.get(BASE_URL + "getSCTOneOpsHealth")
        .then(response => {
            let data = response.data;
            for (let i = 0; i < data.length; i++) {
                data[i].expand = false;
            }
            dispatch({
                type: ONEOPS_HEALTH,
                payload: {
                    oneopsHealthLoading: false,
                    health: data
                }
            });
        })
        .catch(error => {
            dispatch({
                type: ONEOPS_GET_ERRORS,
                payload: error
            });
        })
}

export const toggleSystem = (health, appName) => dispatch => {
    for(let i=0; i<health.length; i++){
        if(health[i].appName===appName){
            health[i].expand = !health[i].expand
        }
    }
    dispatch({
        type: ONEOPS_HEALTH,
        payload: {
            oneopsHealthLoading: false,
            health: health
        }
    });

}
