import React, { Component } from 'react';

import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { withRouter } from 'react-router-dom';
import { logout } from '../../actions/auth-actions'
import logo from '../../assets/walmart-logo-home.png'
import { Button, Dropdown } from 'semantic-ui-react';
import 'semantic-ui-css/semantic.min.css'
import './Navbar.css';
import { getUserAccess } from '../../actions/auth-actions'
const optionsAdmin = [
    { key: 'setting', icon: 'setting', text: 'Settings', value: 'setting' },
    { key: 'logout', icon: 'sign out alternate', text: 'Logout', value: 'logout' },
    { key: 'delete', icon: 'setting', text: 'Manage Access', value: 'manageAccess' }
]
const options = [
    { key: 'setting', icon: 'setting', text: 'Settings', value: 'setting' },
    { key: 'logout', icon: 'sign out alternate', text: 'Logout', value: 'logout' }
]
class Navbar extends Component {
    componentDidMount() {
        this.props.getUserAccess()
    }

    render() {
        let { auth } = this.props;
        return(
            <div className="navigation">
                <div className="navigationLeft">
                    <div className="navigationLogo">
                        <img src={logo} alt="home-logo"/>
                    </div>
                    <div className="navigationToggle">
                        {/*<img src={toggleButton} alt="home-toggle-button"/>*/}
                        <div className={"appName"}>NIRVANA</div>
                    </div>
                </div>
                <div className="navigationRight">
                    {localStorage.getItem('nirvanaUsername') && <Button.Group>
                        <Button>{localStorage.getItem('nirvanaUsername')}</Button>
                        {auth.userAccess && <Dropdown
                            className='button icon'
                            options={auth.userAccess.includes("ADMIN")?optionsAdmin:options}
                            trigger={<React.Fragment />}
                            onChange={(item, data) => {
                                if(data.value==="manageAccess"){
                                    this.props.history.push('/manageAccess');
                                }
                                else if(data.value==="logout"){
                                    this.props.logout()
                                }
                            }}
                        />}
                    </Button.Group>
                    }
                </div>
            </div>
        )
    }
}
Navbar.propTypes = {
    logout: PropTypes.func.isRequired,
    getUserAccess: PropTypes.func.isRequired,
    auth:PropTypes.object.isRequired
}

const mapStateToProps = state  => ({
    auth:state.auth
})

export default connect(mapStateToProps,{ logout, getUserAccess })(withRouter(Navbar))