import {DATA_LOADING, GET_REPORT_DETAILS, GET_REPORTS, GET_USER_REPORTS, REPORTS_LOADING, GET_ISSUES_TREND} from '../actions/constants';

const initialState = {

};

export default function(state = initialState, action) {
    switch(action.type) {
        case GET_REPORTS:
            return {
                ...state,
                loading:action.payload.loading,
                reports: action.payload.reports,
                userReports:action.payload.userReports,
                allSystems:action.payload.allSystems,
                allUsers:action.payload.allUsers
            };
        case REPORTS_LOADING:
            return {
                ...state,
                loading:action.payload.loading
            };
        case GET_USER_REPORTS:
            return {
                ...state,
                loading:action.payload.loading,
                userReports: action.payload.userReports
            }
        case DATA_LOADING:
            return {
                ...state,
                loading: action.payload.loading
            }
        case GET_REPORT_DETAILS:
            return {
                ...state,
                loading: action.payload.loading,
                reportDetails: action.payload.reportDetails
            }
        case GET_ISSUES_TREND:
            return {
                ...state,
                loading: action.payload.loading,
                issuesTrend: action.payload.issuesTrend
            }
        default:
            return state;
    }
}